import { useState, useEffect } from "react";
import axios from "axios";
import { BASE_API_ } from "../../utils/base-url.config";

const PassResetDetails = (validate) => {
  const [values, setValues] = useState({
    email: ""
  });

  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);
  const [successMsg, setSuccessMsg] = useState("");
  //changing the input Values
  const handleChange = (e) => {
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validate(values));
    setIsSubmit(true);
  };

  useEffect(() => {
  
    if (Object.keys(errors).length === 0 && isSubmitting) {
     
      axios
        .post(`${BASE_API_}/user/v1/reset/`, values)
        .then((success) => {
          setSuccessMsg(`Thank you, Check your Email-${values.email} `);
          setIsSubmit(false);
        })
        .catch((error) => {
          setSubmitError([error]);
          setIsSubmit(false);
        });
    } else {
       
      setIsSubmit(false);
    }
  }, [errors, isSubmitting, values]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    successMsg,
    isSubmitting,
  };
};

export default PassResetDetails;
