/* eslint-disable react-hooks/exhaustive-deps */

import React, { useEffect } from "react";
import {
  Wrapper,
  Container,
  FormContactLabel,
  FormWrapper,
  ShippingLabel,
  FormContent1,
  FormButton,
  FormError,
  FormContent2,
  SummaryContent3,
  SummaryWrap,
} from "./styles";
import Loader from "react-loader-spinner";
import validate from "../../FormValidation/pickupValidate";
import FormDetailCheck from "../../FormValidation/pickup";
const ShippingForm = ({ submitForm, priceSetting }) => {
  const {
    values,
    errors,
    handleChange,
    handleSubmit,
    getInfo,
    isSubmitting,
    shippingCity,
    shippingCourier,
    shippingAgentLocation,
    selectedAgents,
    shippingAgents,
    shippingPrice,
    cityName,
    courierName,
    AgentName,
    agentLocation,
    pickupAgent,
  } = FormDetailCheck(submitForm, validate, priceSetting);

  useEffect(() => {
    getInfo();
  }, []);

  let agentsNames = AgentName;

  if (Object.keys(agentsNames).length > 10) {
    agentsNames = AgentName.slice(0, 10) + "...";
  } else {
    agentsNames = AgentName;
  }

  return (
    <Wrapper>
      <Container>
        <form onSubmit={handleSubmit}>
          <FormWrapper>
            <FormContactLabel>
              <h1>Shipping Options</h1>
            </FormContactLabel>
          </FormWrapper>
          <FormWrapper>
            <FormContactLabel>
              <h1>Courier</h1>
            </FormContactLabel>

            <select
              value={values.courier}
              onChange={handleChange}
              name="courier"
              selected=""
            >
              <option value="">select courier</option>
              {shippingCourier.map((data) => {
                return (
                  <option value={data._id} key={data._id}>
                    {data.courier}
                  </option>
                );
              })}
            </select>

            {errors.courierName && <FormError>{errors.courierName}</FormError>}
          </FormWrapper>

          <FormWrapper>
            <FormContactLabel>
              <h1>City</h1>
            </FormContactLabel>

            <select value={values.city} onChange={handleChange} name="city">
              <option value="">select city</option>
              {shippingCity.map((data) => {
                return (
                  <option value={data._id} key={data._id}>
                    {data.location}
                  </option>
                );
              })}
            </select>
            {errors.cityName && <FormError>{errors.cityName}</FormError>}
          </FormWrapper>

          {shippingAgents.length !== 0 && (
            <>
              <ShippingLabel>
                <h1>Pick-up agent options</h1>
              </ShippingLabel>

              <FormContent1>
                <FormContent2>
                  <select
                    value={agentLocation}
                    onChange={handleChange}
                    name="agentLocation"
                  >
                    <option value="">Select Agent Location</option>
                    {shippingAgentLocation.map((data, index) => {
                      return <option value={data}>{data}</option>;
                    })}
                  </select>
                  {errors.agentLocation && (
                    <FormError>{errors.agentLocation}</FormError>
                  )}
                </FormContent2>
                <FormContent2>
                  <select
                    value={pickupAgent}
                    onChange={handleChange}
                    name="pickupAgent"
                  >
                    <option value="">select agent</option>
                    {selectedAgents.map((data, index) => {
                      return (
                        <option value={data._id} key={data._id}>
                          {data.pickUpAgent}
                        </option>
                      );
                    })}
                  </select>
                  {errors.pickupAgent && (
                    <FormError>{errors.pickupAgent}</FormError>
                  )}
                </FormContent2>
              </FormContent1>
            </>
          )}

          <SummaryContent3>
            <SummaryWrap>
              <div>
                <h1>Location</h1>
                <h5>{cityName}</h5>
              </div>
              <div>
                <h1>Courier Provider</h1>
                <h5>{courierName}</h5>
              </div>

              <div>
                <h1>Pick-up Agent</h1>
                <h5>{AgentName ? agentsNames : "---"}</h5>
              </div>

              <div>
                <h1>Cost</h1>
                <h5> {shippingPrice} KES</h5>
              </div>
            </SummaryWrap>
          </SummaryContent3>

          {isSubmitting ? (
            <FormButton>
              {" "}
              <Loader type="TailSpin" color="white" height={30} width={30} />
            </FormButton>
          ) : (
            <FormButton type="submit">Continue to Payment</FormButton>
          )}
        </form>
      </Container>
    </Wrapper>
  );
};

export default ShippingForm;
