import React from "react";
import {  FooterSection } from "../components/common";
import MobileMenu from "../components/common/MobileNav";
import Navigation from "../components/common/Navigation";
import ProductSection from "../components/Productlists";
import { connect } from "react-redux";
import {
  fetchSingleProduct,
  fetchCartProducts,
  fetchSubTotal,
  fetchCartNumber,
  getRelatedProducts,
  fetchSocials,
} from "../store/actions";
import MiniCart from "../components/common/MiniCart";


class Productlist extends React.Component {
  state = {
    openMobileMenu: false,
    miniCartDisplay:false,
    id: this.props.match.params.id,
    products: this.props.products,
    cart: this.props.cart,
    quantity: this.props.products.quantity,
  };

  componentDidMount() {
    this.props.fetchSingleProduct(this.state.id);
    this.props.fetchSubTotal();
    this.props.fetchCartNumber();
    this.props.getRelatedProducts(this.state.id);
     this.props.fetchSocials();
  }

  toggle = () => {
    this.setState({ openMobileMenu: !this.state.openMobileMenu });
  };

  componentDidUpdate(prevProps) {
    if(this.props.match.params.id !== prevProps.match.params.id){
      window.location.reload();
      //  this.props.fetchSingleProduct(this.props.match.params.id);
      //  this.props.getRelatedProducts(this.props.match.params.id);
       
    }
  }

  fetchingCartProducts = () => {
    this.props.fetchCartProducts();
    this.props.fetchSubTotal();
    this.props.fetchCartNumber();
  };

  removeItem = (id) => {
    
    let initialCart = this.props.cart;

    initialCart.some((data, index) => {
      if (data._id === id) {
        initialCart.splice(index, 1);
        localStorage.setItem("cart", JSON.stringify(initialCart));
        this.setState({ cart: initialCart });
        window.location.reload();
        return data;
      }
      return null;
    });
  };

  openMiniDisplay=()=>{
    this.setState({ miniCartDisplay: !this.state.miniCartDisplay });
  }
  openMiniDisplay2=()=>{
    this.setState({ miniCartDisplay: true });
  }
  render() {
    return (
      <>
        <MiniCart
          cart={this.props.cart}
          removeItem={this.removeItem}
          subTotal={this.props.subTotal}
          miniCartDisplay={this.state.miniCartDisplay}
          toggle={this.openMiniDisplay}
        />
        <MobileMenu
          mobileactive={this.state.openMobileMenu}
          toggle={this.toggle}
        />
        <Navigation toggle={this.toggle} cartNum={this.props.cartNum} />
        <ProductSection
          product={this.props.products}
          quantity={this.props.products.quantity}
          fetchCart={this.fetchingCartProducts}
          toggle={this.openMiniDisplay2}
          related={this.props.relatedP}
        />
        <FooterSection socials={this.props.socials} />
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    products: state.singleProduct,
    cart: state.cart,
    subTotal: state.subTotal,
    cartNum: state.cartNum,
    relatedP: state.related,
    socials: state.socials,
  };
};
export default connect(mapStateToProps, {
  fetchSingleProduct,
  fetchCartProducts,
  fetchSubTotal,
  fetchCartNumber,
  getRelatedProducts,
  fetchSocials,
})(Productlist);
