import React from "react";
import {
  Wrapper,
  Container,
  FormContentWrap,
  FormContent1,
  FormButton,
  FormError,
  Success,
  ErrorMsg,
} from "./style";

import Loader from "react-loader-spinner";
import PassFormDetails from "../../FormValidation/passDetail";
import { validatePass } from "../../FormValidation/validateAcdetail";
const PassDetail = () => {
  const formSubmit = () => {};
  const {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    successMsg,
    isSubmitting,
  } = PassFormDetails(formSubmit, validatePass);
  return (
    <Wrapper>
      <Container>
        <form onSubmit={handleSubmit}>
          {successMsg && <Success>{successMsg}</Success>}
          {submitError.length !== 0 && submitError[0].response && (
            <ErrorMsg>{submitError[0].response.data}</ErrorMsg>
          )}
          {submitError.length !== 0 && !submitError[0].response && (
            <ErrorMsg>{submitError[0].message}, check your connection</ErrorMsg>
          )}
          <FormContentWrap>
            <FormContent1>
              <label>Current password</label>
              <input
                type="password"
                name="currentPass"
                value={values.currentPass}
                onChange={handleChange}
              />
              {errors.currentPass && (
                <FormError>{errors.currentPass}</FormError>
              )}
            </FormContent1>
          </FormContentWrap>

          <FormContentWrap>
            <FormContent1>
              <label>New password</label>
              <input
                type="password"
                name="newPass"
                value={values.newPass}
                onChange={handleChange}
              />
              {errors.newPass && <FormError>{errors.newPass}</FormError>}
            </FormContent1>
          </FormContentWrap>

          <FormContentWrap>
            <FormContent1>
              <label>Confirm new password</label>
              <input
                type="password"
                name="confirmPass"
                value={values.confirmPass}
                onChange={handleChange}
              />
              {errors.confirmPass && (
                <FormError>{errors.confirmPass}</FormError>
              )}
            </FormContent1>
          </FormContentWrap>
          {isSubmitting ? (
            <FormButton>
              {" "}
              <Loader type="TailSpin" color="white" height={30} width={30} />
            </FormButton>
          ) : (
            <FormButton type="submit">Save Changes</FormButton>
          )}
        </form>
      </Container>
    </Wrapper>
  );
};

export default PassDetail;
