import React from "react";
import {
  Wrapper,
  Container,
  PHead,
  PSelection,
  SortWrap,
  PriceWrap,
  ProdSelection,
  ProdWrap,
  ProdContent,
  ProdImage,
  ProdText,
  ProdP,
  EmptySection,
  EmptyCartContentWrap,
  EmptyCartContent,
  EmptyImage,
  EmptyText,
} from "./style";

import { connect } from "react-redux";
import { fetchByCategory } from "../../store/actions";
import { HiOutlineShoppingBag } from "react-icons/hi";
import Pagination from "./pagination";
import { BASE_API_ } from "../../utils/base-url.config";

class ProductByCategory extends React.Component {
  state = { sortedData: [],  sortOrder:""};
  componentDidMount() {
    
        if (this.props.productsCollected.length !== 0) {
          if (this.state.sortOrder === "A-Z" || !this.state.sortOrder) {
            const dataSort = this.props.productsCollected.sort(function (
              productA,
              productB
            ) {
              if (productA.name.toLowerCase() < productB.name.toLowerCase())
                return -1;
              if (productA.name.toLowerCase() > productB.name.toLowerCase())
                return 1;
              return 0;
            });

            this.setState({ sortedData: dataSort });
          } else {
              const dataSort = this.props.productsCollected.sort(function (
                productA,
                productB
              ) {
                if (productA.name.toLowerCase() < productB.name.toLowerCase())
                  return 1;
                if (productA.name.toLowerCase() > productB.name.toLowerCase())
                  return -1;
                return 0;
              });

              this.setState({ sortedData: dataSort });  
          }
    } 
  
  }

  componentDidUpdate(prevProps) {
    if (this.props.productsCollected !== prevProps.productsCollected) {
          if (this.props.productsCollected.length !== 0) {
            if (this.state.sortOrder === "A-Z" || !this.state.sortOrder) {
              const dataSort = this.props.productsCollected.sort(function (
                productA,
                productB
              ) {
                if (productA.name.toLowerCase() < productB.name.toLowerCase())
                  return -1;
                if (productA.name.toLowerCase() > productB.name.toLowerCase())
                  return 1;
                return 0;
              });

              this.setState({ sortedData: dataSort });
            } else {
              const dataSort = this.props.productsCollected.sort(function (
                productA,
                productB
              ) {
                if (productA.name.toLowerCase() < productB.name.toLowerCase())
                  return 1;
                if (productA.name.toLowerCase() > productB.name.toLowerCase())
                  return -1;
                return 0;
              });

              this.setState({ sortedData: dataSort });
            }
          } 
    }
     
  }
  handleChange = (e) => {
    
    this.setState({ sortOrder: e.target.value });
    if(e.target.value === 'Z-A'){
          if (this.props.productsCollected.length !== 0) {
            const dataSort = this.props.productsCollected.sort(function (
              productA,
              productB
            ) {
              if (productA.name.toLowerCase() < productB.name.toLowerCase())
                return 1;
              if (productA.name.toLowerCase() > productB.name.toLowerCase())
                return -1;
              return 0;
            });

            this.setState({ sortedData: dataSort });
          }
    } else {
        if (this.props.productsCollected.length !== 0) {
          const dataSort = this.props.productsCollected.sort(function (
            productA,
            productB
          ) {
            if (productA.name.toLowerCase() < productB.name.toLowerCase())
              return -1;
            if (productA.name.toLowerCase() > productB.name.toLowerCase())
              return 1;
            return 0;
          });

          this.setState({ sortedData: dataSort });
        }
    }
  };
  getall = () => {
   
  };
  render() {
    return (
      <Wrapper>
        <Container>
          <PHead>
            <h1>{this.props.categoryName}</h1>
          </PHead>
          <PSelection>
            <SortWrap>
              <form>
                <label htmlFor="sort">SORT BY</label>
                <select id="sort" name="sort" onChange={this.handleChange}>
                  <option value="A-Z">Alphabetically, A-Z</option>
                  <option value="Z-A">Alphabetically, Z-A</option>
                </select>
              </form>
              <PriceWrap>
                <h1>{this.props.totalProducts} products</h1>
              </PriceWrap>
            </SortWrap>
          </PSelection>
        </Container>
        {this.props.productsCollected.length === 0 ? (
          <EmptySection>
            <EmptyCartContentWrap>
              <EmptyCartContent>
                <EmptyImage>
                  <HiOutlineShoppingBag />
                </EmptyImage>
                <EmptyText>No Products.</EmptyText>
              </EmptyCartContent>
            </EmptyCartContentWrap>
          </EmptySection>
        ) : (
          <ProdSelection>
            <ProdWrap>
              {this.state.sortedData.map((data, index) => {
                return (
                  <ProdContent key={data._id} to={`/${data.name}/${data._id}`}>
                    <ProdImage>
                      {data.featuredImage ? (
                        <img
                          src={`${BASE_API_}${data.featuredImage.path}`}
                          alt=""
                        />
                      ) : (
                        <img src={data.image} alt="" />
                      )}
                    </ProdImage>
                    <ProdText>{data.name}</ProdText>
                    <ProdP>{data.price} KES</ProdP>
                  </ProdContent>
                );
              })}
            </ProdWrap>
            <Pagination
              totalProducts={this.props.totalProducts}
              productsPerPage={this.props.productsPerPage}
              current={this.props.current}
              paramslinks={this.props.paramslinks}
              categoryName={this.props.categoryName}
              identity={this.props.identity}
            />
          </ProdSelection>
        )}
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    selectedCategory: state.selectedCategory,
  };
};

export default connect(mapStateToProps, { fetchByCategory })(ProductByCategory);
