import { useState, useEffect } from "react";
import axios from "axios";
import { BASE_API_ } from "../../utils/base-url.config";

const NewPassFormDetails = (Formsubmit, token, validate) => {
  const [values, setValues] = useState({
    newPass: "",
    confirmPass: "",
  });

  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);
  const [successMsg, setSuccessMsg] = useState("");
  //changing the input Values
  const handleChange = (e) => {
    if (
      e.target.name === "confirmPass" &&
      Object.keys(values.newPass).length !== 0
    ) {
      if (e.target.value !== values.newPass) {
        setErrors({
          confirmPass: "password doesnot match",
        });
      } else if (e.target.value === values.newPass) {
        setErrors({
          confirmPass: undefined,
        });
      }
    }
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validate(values));
    setIsSubmit(true);
  };

  useEffect(() => {
    if (Object.keys(errors).length === 0 && isSubmitting) {
      axios
        .put(`${BASE_API_}/user/v1/newpassKey/${token}`, values)
        .then((success) => {
          setSuccessMsg("successfully updated");
          setIsSubmit(false);
          Formsubmit();
        })
        .catch((error) => {
          setSubmitError([error]);
          setIsSubmit(false);
        });
    } else {
      setIsSubmit(false);
    }
  }, [errors, isSubmitting, values, Formsubmit,token]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    successMsg,
    isSubmitting,
  };
};

export default NewPassFormDetails;
