import React from "react";
import { Wrapper, Container, LoadingWrapper, ErrorWrapper } from "./styles";
import Loader from "react-loader-spinner";
const VerifyToken = ({ loading, errors }) => {
  return (
    <Wrapper>
      <Container>
        {loading && errors.length === 0 ? (
          <LoadingWrapper>
            <Loader type="TailSpin" color="red" height={100} width={100} />
            <h1>Verifying</h1>
          </LoadingWrapper>
        ) : (
          <ErrorWrapper>
            <h1>Verification Message</h1>
            {errors[0].response ? (
              <p>{errors[0].response.data}</p>
            ) : (
              <p>{errors[0].message}, please check your internet connection.</p>
            )}
          </ErrorWrapper>
        )}
      </Container>
    </Wrapper>
  );
};

export default VerifyToken;
