import React, { useEffect, useState } from "react";
import {
  Wrapper,
  Container,
  PHead,
  EmptySection,
  EmptyCartContentWrap,
  EmptyCartContent,
  ContinueShopping,
  EmptyImage,
  EmptyText,
  CartWrapper,
  TitleWrapper,
  ProductWrapper,
  ProductWrap,
  ProductContent,
  ProductImage,
  ProductName,
  ProductPrice,
  ProductQuantity,
  ProductSubTotal,
  Line,
  Button,
  PNTitle,
  PPTitle,
  PQTitle,
  PSTitle,
  PriceWrapper,
  PriceWrapContent,
  TotalPrice,
  PriceWrapText,
  PriceWrapButton,
  Line1,
  Line2,
} from "./style";

import { HiOutlineShoppingBag } from "react-icons/hi";
import NoImage from "../../assets/NoImage.jpg";
import { BASE_API_ } from "../../utils/base-url.config";

const CartSection = ({ cartNum, subTotal, cart, removeItem }) => {
  const [cartEmpty, setCartEmpty] = useState(false);

  useEffect(() => {
    if (cartNum === 0) {
      setCartEmpty(true);
    }
  }, [cartNum]);
  return (
    <Wrapper>
      <Container>
        <PHead>
          <h1>Shopping Cart</h1>
        </PHead>
        {cartEmpty ? (
          <EmptySection>
            <EmptyCartContentWrap>
              <EmptyCartContent>
                <EmptyImage>
                  <HiOutlineShoppingBag />
                </EmptyImage>
                <EmptyText>Your cart is empty.</EmptyText>
              </EmptyCartContent>
              <ContinueShopping to="/products">
                CONTINUE SHOPPING
              </ContinueShopping>
            </EmptyCartContentWrap>
          </EmptySection>
        ) : (
          <CartWrapper>
            <TitleWrapper>
              <PNTitle>Product name</PNTitle>
              <PPTitle>Price</PPTitle>
              <PQTitle>Quantity</PQTitle>
              <PSTitle>Subtotal</PSTitle>
            </TitleWrapper>

            <ProductWrapper>
              <ProductWrap>
                {cart.map((data, index) => {
                   let pName = data.name;
                   if (Object.keys(pName).length > 20) {
                     pName = data.name.slice(0, 20) + "...";
                   }
                  return (
                    <ProductContent key={index}>
                      <Line1 />
                      <Line2 />
                      <ProductImage>
                        {data.productImage ? (
                          <img
                            src={`${BASE_API_}${data.productImage.path}`}
                            alt="imsgd"
                          />
                        ) : (
                          <img src={NoImage} alt="NoImage" />
                        )}
                      </ProductImage>

                      <ProductName>
                        <h1>
                          {pName} <br />
                          {data.colortype && <p>Color: {data.colortype} </p>}
                          {data.productSize && (
                            <p>
                              Size: {data.productSize ? data.productSize : null}
                            </p>
                          )}
                        </h1>
                      </ProductName>

                      <ProductPrice>
                        <h1>{data.price} KES</h1>
                      </ProductPrice>

                      <ProductQuantity>
                        <input type="text" value={data.quantity} readOnly />
                        <Button onClick={() => removeItem(data._id)}>
                          Remove
                        </Button>
                      </ProductQuantity>

                      <ProductSubTotal>
                        <h1>{data.subtotal} KES</h1>
                      </ProductSubTotal>
                    </ProductContent>
                  );
                })}
              </ProductWrap>
              <Line />
              <PriceWrapper>
                <PriceWrapContent>
                  <TotalPrice>{subTotal} KES</TotalPrice>
                  <PriceWrapText>
                    Shipping and taxes calculated at checkout
                  </PriceWrapText>
                  <PriceWrapButton to="/checkout">CHECKOUT</PriceWrapButton>
                </PriceWrapContent>
              </PriceWrapper>
            </ProductWrapper>
          </CartWrapper>
        )}
      </Container>
    </Wrapper>
  );
};

export default CartSection;
