import { useState, useEffect } from "react";
import axios from "axios";
import Cookies from 'js-cookie'
import { BASE_API_ } from "../../utils/base-url.config";

const AddressFormDetails = (Formsubmit, validate) => {
  const [values, setValues] = useState({
    firstname: "",
    lastname: "",
    email: "",
    address: "",
    phonenumber: "",
    region:"",
    city:""
    
  });

  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);
const [successMsg, setSuccessMsg] = useState("");
  //changing the input Values
  const handleChange = (e) => {
  
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validate(values));
    setIsSubmit(true);
  };

  const getInfo = () => {
    const getInfo = JSON.parse(Cookies.get("shipping"));
    const getUser = JSON.parse(Cookies.get("user"));
    if (getInfo && getInfo !== {} && getUser) {
     
      setValues({
        ...getInfo,
        firstname: getUser.firstname,
        lastname: getUser.lastname,
        phonenumber: getUser.phonenumber,
      });
    }else if (getInfo && getInfo !== {} && !getUser) {
      setValues({
        ...values,
        ...getInfo
      });
    } else {
         setValues({
           ...values,
           firstname: getUser.firstname,
           lastname: getUser.lastname,
           phonenumber: getUser.phonenumber,
         });
    }
  };

  useEffect(() => {
   
    if (Object.keys(errors).length === 0 && isSubmitting) {
      const getId = Cookies.get("sid");
      const getToken = Cookies.get("_tk");
      axios
        .put(`${BASE_API_}/user/v1/adupdate/${getId}`, values, {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        })
        .then((success) => {
          setSuccessMsg("successfully updated");
          Cookies.set("shipping", values, { expires: 1 });
          Formsubmit();
          setIsSubmit(false);
        })
        .catch((error) => {
          setSubmitError([error]);
          setIsSubmit(false);
        });
    } else {
      setIsSubmit(false);
    }
  }, [errors, isSubmitting, values, Formsubmit]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    getInfo,
    successMsg,
    isSubmitting,
  };
};

export default AddressFormDetails;
