/* eslint-disable no-unused-vars */
import React from 'react'
import {connect} from 'react-redux';
import {
  Wrapper,
  Container,
  PHead,
  PSelection,
  SortWrap,
  PriceWrap,
  ProdSelection,
  ProdWrap,
  ProdContent,
  ProdImage,
  ProdText,
  ProdP,
  EmptySection,
  EmptyCartContentWrap,
  EmptyCartContent,
  EmptyImage,
  EmptyText,
  IconButton,
  CloseButton,
} from "./styles";
import { HiOutlineShoppingBag } from "react-icons/hi";
import { IoMdClose } from "react-icons/io";
import SearchSvg from "../../assets/Search.svg";
import Pagination from "./pagination";
import { searchProducts } from "../../store/actions";
import Cookies from 'js-cookie';
import { BASE_API_ } from "../../utils/base-url.config";

class SearchPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      query: "",
      results: [],
      loading: false,
    };
  }

  componentDidMount() {
    const getQuery = Cookies.get("searchquery");
    const getProductItems = JSON.parse(
      localStorage.getItem("searchResults") || "[]"
    );
    this.setState({ results: getProductItems, query: getQuery });
      this.props.searchProducts();
  }

  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
        loading: true,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    if (this.props.searchAllProducts !== 0) {
      const allProducts = this.props.searchAllProducts;
      const resultData = allProducts.filter((products) => {
        let productName = products.name.toLowerCase();
        return productName.includes(query.toLowerCase());
      });
      const totalProducts = resultData.length;
      
      localStorage.setItem("searchResults", JSON.stringify(resultData));
      Cookies.set("searchquery", query);
      
      let showProducts = resultData;
    
      this.setState({
        results: showProducts,
        loading: false,
        allResults: totalProducts,
      });
    } else {
    }
  };

  render() {
    return (
      <Wrapper>
        <Container>
          <PHead>
            <h1>Search Results</h1>
          </PHead>
          <PSelection>
            <SortWrap>
              <form onSubmit={this.onFormSubmit}>
                <input
                  type="text"
                  value={this.state.query}
                  name="query"
                  onChange={this.handleInputChange}
                  placeholder="what are you looking for?"
                />
                <button>
                  <IconButton>
                    <img src={SearchSvg} alt="" />
                  </IconButton>
                </button>
              </form>
              <PriceWrap>
                <h1>
                  {this.state.results.length} results for {this.state.query}
                </h1>
              </PriceWrap>
            </SortWrap>
          </PSelection>
        </Container>
        {this.state.results.length === 0 ? (
          <EmptySection>
            <EmptyCartContentWrap>
              <EmptyCartContent>
                <EmptyImage>
                  <HiOutlineShoppingBag />
                </EmptyImage>
                <EmptyText>No Products.</EmptyText>
              </EmptyCartContent>
            </EmptyCartContentWrap>
          </EmptySection>
        ) : (
          <ProdSelection>
            <ProdWrap>
              {this.state.results.map((data, index) => {
                return (
                  <ProdContent key={data._id} to={`/${data.name}/${data._id}`}>
                    <ProdImage>
                      {data.featuredImage ? (
                        <img
                          src={`${BASE_API_}${data.featuredImage.path}`}
                          alt=""
                        />
                      ) : (
                        <img src={data.image} alt="" />
                      )}
                    </ProdImage>
                    <ProdText>{data.name}</ProdText>
                    <ProdP>{data.price} KES</ProdP>
                  </ProdContent>
                );
              })}
            </ProdWrap>
            {
              //      <Pagination
              //   totalProducts={this.state.results.length}
              //   productsPerPage={this.state.itemsperPage}
              //   current={this.state.current}
              //   paramslinks={this.props.paramslinks}
              // />
            }
          </ProdSelection>
        )}
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return { searchAllProducts: state.searchallproducts };
};
export default connect(mapStateToProps, {
  searchProducts,
})(SearchPage);
