import  { useState, useEffect } from "react";

import axios from 'axios';
import { BASE_API_ } from "../../utils/base-url.config";

const RegisterDetails = (FormSubmit, validate) => {
  const [values, setValues] = useState({
    email: "",
    firstname: "",
    lastname: "",
    password: "",
    Cpassword: "",
  });

  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);

  const handleChange = (e) => {
    if (
      e.target.name === "Cpassword" &&
      Object.keys(values.password).length !== 0
    ) {
      if (e.target.value !== values.password) {
        setErrors({
          Cpassword: "password doesnot match",
        });
      } else if (e.target.value === values.password) {
        setErrors({
          Cpassword: undefined,
        });
      }
    }
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
   
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validate(values));
    setIsSubmit(true);
  };

  useEffect(() => {
    
    if (Object.keys(errors).length === 0 && isSubmitting) {
      const postItem = async () => {
        try {
          await axios
            .post(`${BASE_API_}/user/registers`, values)
            .then((sucess) => {
              // let data = sucess.data;
              // localStorage.setItem("_tk", JSON.stringify(data.token));
              // localStorage.setItem("sid", JSON.stringify(data.id));
              FormSubmit();
            })
            .catch((error) => {
              setSubmitError([error]);
              setIsSubmit(false);
            });
        } catch (error) {
          setIsSubmit(false);
        }
      };

      postItem();
    } else {
      setIsSubmit(false);
    }
  }, [errors, values, isSubmitting, FormSubmit]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    isSubmitting,
  };
};

export default RegisterDetails;
