import React from "react";
import {
  Wrapper,
  Container,
  FormContentWrap,
  FormContent1,
  FormButton,
  FormError,
  Success,
  ErrorMsg,
  Success2,
} from "./styles";

import Loader from "react-loader-spinner";
import PassFormDetails from "../../FormValidation/newPassKey";
import { validateNewPass } from "../../FormValidation/validateNewPassKey";
const PassReset = ({ tk, formsubmit }) => {
  const {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    successMsg,
    isSubmitting,
  } = PassFormDetails(formsubmit, tk, validateNewPass);
  return (
    <Wrapper>
      <Container>
        <form onSubmit={handleSubmit}>
          {successMsg && <Success>{successMsg}</Success>}
          {submitError.length !== 0 && submitError[0].response && (
            <ErrorMsg>{submitError[0].response.data}</ErrorMsg>
          )}
          {submitError.length !== 0 && !submitError[0].response && (
            <ErrorMsg>{submitError[0].message}, check your connection</ErrorMsg>
          )}
        
            <Success2>We'll redirect you after password rest is successful.</Success2>
          <FormContentWrap>
          
            <FormContent1>
              <label>New password</label>
              <input
                type="password"
                name="newPass"
                value={values.newPass}
                onChange={handleChange}
              />
              {errors.newPass && <FormError>{errors.newPass}</FormError>}
            </FormContent1>
          </FormContentWrap>

          <FormContentWrap>
            <FormContent1>
              <label>Confirm new password</label>
              <input
                type="password"
                name="confirmPass"
                value={values.confirmPass}
                onChange={handleChange}
              />
              {errors.confirmPass && (
                <FormError>{errors.confirmPass}</FormError>
              )}
            </FormContent1>
          </FormContentWrap>
          {isSubmitting ? (
            <FormButton>
              {" "}
              <Loader type="TailSpin" color="white" height={30} width={30} />
            </FormButton>
          ) : (
            <FormButton type="submit">Submit</FormButton>
          )}
        </form>
      </Container>
    </Wrapper>
  );
};

export default PassReset;
