import axios from "axios";
import React from "react";
import VerifyToken from "../components/passwordComponentts/verifyToken";
import { BASE_API_ } from "../utils/base-url.config";

class PassTVerification extends React.Component {
  state = { loading: true, errors: [] };

  componentDidMount() {
    const verification = async () => {
      let token = this.props.match.params.id;
      await axios
        .get(`${BASE_API_}/user/v1/resetToken/` + token)
        .then((success) => {
          this.props.history.replace(`/passreset/${token}`);
        })
        .catch((error) => {
          this.setState({ loading: false, errors: [error] });
        });
    };

    verification();
    
  }

  render() {
    return (
      <>
        <VerifyToken errors={this.state.errors} loading={this.state.loading}/>
      </>
    );
  }
}

export default PassTVerification;
