/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import {
  Wrapper,
  Container,
  Label,
  ErrorMsg,
  FormContent,
  FormContent1,
  FormButton,
  FormContentWrap,
  FormButtonWrap,
  ButtonContentWrap,
  ButtonText,
  FormContentWrap2,
  FormContentWrap3,
} from "./style";
import Loader from "react-loader-spinner";
import validate from "../../../validation2";
import FormDetails2 from "../../FormValidation/Form2";
const MobilePay = ({ FormSuccess, subTotal, shippingPrice }) => {


  const {
    values,
    errors,
    submitError,
    isSubmitting,
    handleChange,
    handleSubmit,
    countryCode,
    getInfo,
  } = FormDetails2(FormSuccess, validate);

  let PriceShipping;
  let total;
  if (shippingPrice) {
    if (subTotal > 1500) {
      PriceShipping = 0;
      total = subTotal;
      sessionStorage.setItem("p", PriceShipping);
    } else {
      PriceShipping = shippingPrice;
      total = subTotal + parseInt(shippingPrice);
      sessionStorage.setItem("p", PriceShipping);
    }
  } else {
    total = subTotal;
  }
  useEffect(() => {
    getInfo();
  }, []);
  return (
    <Wrapper>
      <Container>
        <form onSubmit={handleSubmit}>
          {submitError.length !== 0 && submitError[0].response && (
            <ErrorMsg>{submitError[0].response.data}</ErrorMsg>
          )}
          {submitError.length !== 0 && !submitError[0].response && (
            <ErrorMsg>{submitError[0].message}, check your connection</ErrorMsg>
          )}
          <Label>Mobile Payment</Label>
          <FormContentWrap3>
            <FormContent>
              <FormContentWrap>
                <select
                  id="payment"
                  name="operator"
                  placeholder="select option"
                  onChange={handleChange}
                  value={values.operator}
                >
                  <option value="">Select your operator</option>
                  <option value="MPESA">MPESA</option>
                </select>
              </FormContentWrap>
            </FormContent>

            {errors.operator && <ErrorMsg>{errors.operator}</ErrorMsg>}
          </FormContentWrap3>

          <FormContentWrap2>
            <FormContent1>
              <input
                className="CCode"
                type="text"
                placeholder="254"
                value={countryCode}
                name="countryCode"
                readOnly
              />
              <input
                className="NNum"
                type="tel"
                placeholder="Phone number"
                value={values.number}
                name="number"
                onChange={handleChange}
              />
            </FormContent1>
            {errors.number && <ErrorMsg>{errors.number}</ErrorMsg>}
          </FormContentWrap2>

          <FormButtonWrap>
            {!isSubmitting ? (
              <FormButton type="submit">Pay now: {total} Kes </FormButton>
            ) : (
              <ButtonContentWrap>
                <ButtonText>
                  Please wait patiently to received an MPESA notification to put
                  in your PIN and confirm your order.{" "}
                </ButtonText>
                <FormButton>
                  {" "}
                  <Loader
                    type="TailSpin"
                    color="white"
                    height={30}
                    width={30}
                  />
                </FormButton>
              </ButtonContentWrap>
            )}
          </FormButtonWrap>
        </form>
      </Container>
    </Wrapper>
  );
};

export default MobilePay;
