import { useEffect, useState } from "react";
import axios from "axios";
import Cookies from "js-cookie";
import { BASE_API_ } from "../../utils/base-url.config";

const Form2 = (FormSubmit, validate) => {
  const [values, setValues] = useState({
    operator: "",
    number: "",
  });

  const [countryCode, setCountryCode] = useState("");

  // eslint-disable-next-line no-unused-vars
  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  //changing the values in the input into state
  const handleChange = (e) => {
    if (e.target.value === "MPESA") {
      setCountryCode("254");
    }
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    let finalValues = { ...values, countryCode: countryCode };
    setErrors(validate(finalValues));
    setIsSubmit(true);
  };
  const getInfo = () => {
    const getToken = Cookies.get("_tk");
    const getId = Cookies.get("sid");
    if (getToken && getId) {
      setIsLoggedIn(true);
    } else {
      setIsLoggedIn(false);
    }
  };

  useEffect(() => {
    if (Object.keys(errors).length === 0 && isSubmitting) {
      let mpesaNumber = countryCode + values.number;

      let finalValues = {
        ...values,
        countryCode: countryCode,
        mpNumber: mpesaNumber,
      };
      Cookies.set("operation", finalValues, { expires: 1 });
      // localStorage.setItem("operation", JSON.stringify(finalValues));
      const getDetails = JSON.parse(localStorage.getItem("cart"));
      const getContactDetails = JSON.parse(Cookies.get("contacts"));

      let finalValues2 = {
        ...finalValues,
        cart: getDetails,
        contact: getContactDetails,
      };

      if (isLoggedIn) {
        const getToken = Cookies.get("_tk");
        const getId = Cookies.get("sid");

        axios
          .post(`${BASE_API_}/user/v1/create/${getId}`, finalValues2, {
            headers: {
              Authorization: "Bearer " + getToken,
            },
          })
          .then((success) => {
            let code = success.data.code;
            Cookies.set("oc", code, { expires: 1 });

            FormSubmit();
          })
          .catch((error) => {
            setSubmitError([error]);
            setIsSubmit(false);
            let errorArray = [];
            errorArray.push(error);
            if (errorArray[0].response && errorArray.length !== 0) {
              if (errorArray[0].response.status === 401) {
                Cookies.remove("_tk");
                Cookies.remove("sid");
                 Cookies.remove("isLoggedIn");
              }
            }
          });
      } else {
        axios
          .post(`${BASE_API_}/gorder/v1/create`, finalValues2)
          .then((success) => {
            let code = success.data.code;
            Cookies.set("oc", code, { expires: 1 });
            FormSubmit();
          })
          .catch((error) => {
            setSubmitError([error]);
            setIsSubmit(false);
          });
      }
    } else {
      setIsSubmit(false);
    }
  }, [errors, values, countryCode, isSubmitting, isLoggedIn, FormSubmit]);

  return {
    values,
    errors,
    submitError,
    isSubmitting,
    handleChange,
    handleSubmit,
    countryCode,
    getInfo,
  };
};

export default Form2;
