import axios from "axios";
import Cookies from "js-cookie";
import { BASE_API_ } from "../../utils/base-url.config";
//get all the featured
export const fetchFeatured = () => {
  return async (dispatch) => {
    const featured = await axios.get(`${BASE_API_}/products/v1/byfeatured`);
    if (featured.data !== null || featured.data !== undefined) {
      dispatch({
        type: "FETCH_FEATURED",
        payload: featured.data,
      });
    } else {
      dispatch({
        type: "FETCH_FEATURED",
        payload: [],
      });
    }
  };
};

