//checkIfUseristhere

export const checkUser = () => {
  //const
  const getToken = Cookies.get("_tk");
  const getId = Cookies.get("sid");

  if (getToken !== undefined && getId !== undefined) {
    return {
      type: "LOG_USER",
      payload: "loggedIn",
    };
  } else {
    return {
      type: "LOG_USER",
      payload: "notloggedIn",
    };
  }
};

export const getUserDetails = (redirect) => {
  return async (dispatch) => {
    try {
      const getToken = Cookies.get("_tk");
      const getId = Cookies.get("sid");
      if (getToken !== undefined && getId !== undefined) {
        const getDetails = await axios.get(
          `${BASE_API_}/user/v1/shipping/${getId}`,
          {
            headers: {
              Authorization: "Bearer " + getToken,
            },
          }
        );

        dispatch({
          type: "GETUSERDETAILS",
          payload: getDetails.data,
        });
      } else {
        dispatch({
          type: "GETUSERDETAILS",
          payload: "",
        });
      }
    } catch (error) {
      let errorArray = [];
      errorArray.push(error);
      if (errorArray[0].response && errorArray.length !== 0) {
        if (
          errorArray[0].response.status === 401 ||
          errorArray[0].response.status === 500
        ) {
          Cookies.remove("_tk");
          Cookies.remove("sid");
          Cookies.remove("isLoggedIn");
          Cookies.remove('user');
          Cookies.remove('contacts')
          Cookies.remove('operation');
          redirect();
        }
      }
    }
  };
};

