import React from "react";
import { Wrapper, FormContainer, ProductContainer } from "./checkstyles";
import CheckHead from "../components/common/CheckHead";
import ShippingForm from "../components/common/ShippingForm";
import CheckFooter from "../components/common/CheckFooter";
import CheckProducts from "../components/common/CheckProducts";

import { connect } from "react-redux";
import { fetchCartProducts, fetchSubTotal } from "../store/actions";
import OrderSummary from "../components/OrderSummary";
class Checkout extends React.Component {
  state = { dropOrders: false };
  componentDidMount() {
    const getCartDetails = async () => {
     await this.props.fetchCartProducts();
    await   this.props.fetchSubTotal();
    if (this.props.cart.length === 0) {
      this.props.history.push("/cart");
    }
    };
    getCartDetails();
  }

  componentDidUpdate(prevProps) {
    if (this.props.cart.length !== prevProps.cart.length) {
     const getCartDetails = async () => {
       await this.props.fetchCartProducts();
       await this.props.fetchSubTotal();
       if (this.props.cart.length === 0) {
         this.props.history.push("/cart");
       }
     };
     getCartDetails();
    }
  }
  submitForm = () => {
    this.props.history.push("/pickup");
  };

  ordersActive = () => {
    this.setState({ dropOrders: !this.state.dropOrders });
  };

  render() {
    return (
      <>
        <Wrapper>
          <FormContainer>
            <OrderSummary
              orderactive={this.state.dropOrders}
              activateOrders={this.ordersActive}
              subtotal={this.props.subTotal}
            />
            <CheckHead Sactive={"true"} />
            <ShippingForm submitForm={this.submitForm} />
            <CheckFooter />
          </FormContainer>

          <ProductContainer orderactive={this.state.dropOrders}>
            <CheckProducts
              cart={this.props.cart}
              subtotal={this.props.subTotal}
            />
          </ProductContainer>
        </Wrapper>
      </>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    products: state.singleProduct,
    cart: state.cart,
    subTotal: state.subTotal,
    cartNum: state.cartNum,
  };
};
export default connect(mapStateToProps, {
  fetchCartProducts,
  fetchSubTotal,
})(Checkout);
