/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import {
  Wrapper,
  Container,
  FormContentWrap,
  FormContent,
  FormContent1,
  FormButton,
  ErrorMsg,
  Success,
  FormError,
} from "./style";

import Loader from "react-loader-spinner";
import AddressFormDetails from "../../FormValidation/addressDetail";
import { validateAddress } from "../../FormValidation/validateAcdetail";
import Cookies from "js-cookie";
const AddressInfo = ({ redirects }) => {
  const formSubmit = () => {};
  const {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    getInfo,
    successMsg,
    isSubmitting,
  } = AddressFormDetails(formSubmit, validateAddress);

  useEffect(() => {
    let getLogin = Cookies.get("_tk");
    if (getLogin ) {
      getInfo();
    } else {
      redirects();
    }
  }, []);
  return (
    <Wrapper>
      <Container>
        <form onSubmit={handleSubmit}>
          {successMsg && <Success>{successMsg}</Success>}
          {submitError.length !== 0 && submitError[0].response && (
            <ErrorMsg>{submitError[0].response.data}</ErrorMsg>
          )}
          {submitError.length !== 0 && !submitError[0].response && (
            <ErrorMsg>{submitError[0].message}, check your connection</ErrorMsg>
          )}
          <FormContentWrap>
            <FormContent>
              <label>First name</label>
              <input
                type="text"
                name="firstname"
                readOnly
                value={values.firstname}
              />
            </FormContent>
            <FormContent>
              <label>last name</label>
              <input
                type="text"
                name="lastname"
                readOnly
                value={values.lastname}
              />
            </FormContent>
          </FormContentWrap>

          <FormContentWrap>
            <FormContent1>
              <label>Phone number</label>
              <input
                type="tel"
                value={values.phonenumber}
                onChange={handleChange}
                name="phonenumber"
              />
              {errors.phonenumber && (
                <FormError>{errors.phonenumber}</FormError>
              )}
            </FormContent1>
          </FormContentWrap>

          <FormContentWrap>
            <FormContent1>
              <label>Physical Address</label>
              <input
                type="text"
                value={values.address}
                onChange={handleChange}
                name="address"
              />
              {errors.address && <FormError>{errors.address}</FormError>}
            </FormContent1>
          </FormContentWrap>

          <FormContentWrap>
            <FormContent>
              <label>Region</label>
              <input
                type="text"
                value={values.region}
                onChange={handleChange}
                name="region"
              />
              {errors.region && <FormError>{errors.region}</FormError>}
            </FormContent>
            <FormContent>
              <label>City</label>
              <input
                type="text"
                value={values.city}
                onChange={handleChange}
                name="city"
              />
              {errors.city && <FormError>{errors.city}</FormError>}
            </FormContent>
          </FormContentWrap>
          {isSubmitting ? (
            <FormButton>
              {" "}
              <Loader type="TailSpin" color="white" height={30} width={30} />
            </FormButton>
          ) : (
            <FormButton type="submit">Save Changes</FormButton>
          )}
        </form>
      </Container>
    </Wrapper>
  );
};

export default AddressInfo;
