import React from "react";
import {
  Wrapper,
  Container,
  ProductWrap,
  ProductContent,
  ProductImage,
  ProductName,
  ProductPrice,
  Line,
  SubTotalWrap,
  SubContentTotal,
  ShippingContentTotal,
  TotalsWrap,
  InnerWrapper,
} from "./style";
import NoImage from "../../../assets/NoImage.jpg";
import { BASE_API_ } from "../../../utils/base-url.config";

const CheckProducts = ({ cart, subtotal, shippingPrice }) => {
  let PriceShipping;
  let total;
  if (shippingPrice) {
    if (subtotal > 1500) {
      PriceShipping = 0;
      total = subtotal;
      sessionStorage.setItem("p", PriceShipping);
    } else {
      PriceShipping = shippingPrice;
      total = subtotal + parseInt(shippingPrice);
      sessionStorage.setItem("p", PriceShipping);
    }
  } else {
     total = subtotal ;
     
  }

  return (
    <Wrapper>
      <InnerWrapper>
        <Container>
          <ProductWrap>
            {cart.map((data, index) => {
                let pName = data.name;
                if (Object.keys(pName).length > 20) {
                  pName = data.name.slice(0, 20) + "...";
                }
              return (
                <ProductContent key={data._id}>
                  <ProductImage>
                    {data.productImage ? (
                      <img
                        src={`${BASE_API_}${data.productImage.path}`}
                        alt={data.productImage.name}
                      />
                    ) : (
                      <img src={NoImage} alt="NoImage" />
                    )}
                    <h1>{data.quantity}</h1>
                  </ProductImage>

                  <ProductName>
                    <h1>
                      {pName} <br />
                      {data.colortype && <p>Color: {data.colortype} </p>}
                      {data.productSize && (
                        <p>
                          Size: {data.productSize ? data.productSize : null}
                        </p>
                      )}
                    </h1>
                  </ProductName>

                  <ProductPrice>
                    <h1>{data.price} KES</h1>
                  </ProductPrice>
                </ProductContent>
              );
            })}
          </ProductWrap>

          <Line />

          <SubTotalWrap>
            <SubContentTotal>
              <h1>Subtotal</h1>
              <h5>{subtotal} KES</h5>
            </SubContentTotal>

            <ShippingContentTotal>
              <h1>Shipping</h1>
              <h5>
                {shippingPrice ? PriceShipping : "Calculated at next step"}
              </h5>
            </ShippingContentTotal>
          </SubTotalWrap>
          <Line />
          <TotalsWrap>
            <h1>Total</h1>
            <h5>{total} KES</h5>
          </TotalsWrap>
        </Container>
      </InnerWrapper>
    </Wrapper>
  );
};

export default CheckProducts;
