import React from "react";
import Processing from "../components/ProcessFailed";
import Cookies from "js-cookie";
class ProcessFailed extends React.Component {
  constructor() {
    super();
    this.timer = null;
    this.state = {
      timeset: 0,
      timedOut: false,
      paymentError: "",
    };
  }
  componentDidMount() {
    this.checkOrder();
  }
  submitSuccess = () => {
    this.props.history.push("/success");
     Cookies.remove("_tk");
     Cookies.remove("sid");
     Cookies.remove("isLoggedIn");
     Cookies.remove("user");
     Cookies.remove("contacts");
     Cookies.remove("operation");
      Cookies.remove("shipping");
  };

  checkOrder = () => {
    let getErrorPay = Cookies.get("ep");
  
    if (!getErrorPay) {
      this.props.history.push("/");
    } else {
        this.setState({paymentError: getErrorPay})
         Cookies.remove("_tk");
         Cookies.remove("sid");
         Cookies.remove("isLoggedIn");
         Cookies.remove("user");
         Cookies.remove("contacts");
         Cookies.remove("operation");
          Cookies.remove("shipping");
    }
  };

  checkResult = async () => {};

  resetTimer = () => {
    this.checkOrder();
  };

  render() {
    return (
      <>
        <Processing errorPay={this.state.paymentError} historyP= {this.props.history}/>
      </>
    );
  }
}

export default ProcessFailed;
