import React from "react";
import {
  Wrapper,
  Container,
  ShoppingCart,
  SummaryText,
  SubTotal,
} from "./style";

import { RiArrowDownSLine, RiArrowUpSLine } from "react-icons/ri";
import shopCart from "../../assets/icons/shoppingCart.svg";
const OrderSummary = ({ subtotal,activateOrders, orderactive }) => {
  return (
    <Wrapper>
      <Container>
        <SummaryText onClick={activateOrders}>
          <ShoppingCart>
            <img src={shopCart} alt="" />
          </ShoppingCart>
          <h1>Show Order Summary</h1>
          {orderactive ? (<RiArrowUpSLine/>): (<RiArrowDownSLine />)}
          
        </SummaryText>

        <SubTotal>
          <h1>{subtotal} KES</h1>
        </SubTotal>
      </Container>
    </Wrapper>
  );
};

export default OrderSummary;
