import React from "react";
import Navigation from "../components/common/Navigation";
import OverView from "../components/AccountOverview/Overview";
import MobileMenu from "../components/common/MobileNav";
import Cookies from "js-cookie";

import { connect } from "react-redux";
import { fetchCartNumber } from "../store/actions";
class AccountOverview extends React.Component {
  state = { openMobileMenu: false };
  componentDidMount() {
    const getLoggedIn = Cookies.get("isLoggedIn");
    if (!getLoggedIn) {
      Cookies.remove("_tk");
      Cookies.remove("sid");
      Cookies.remove("user");
      this.redirects();
    }
    this.props.fetchCartNumber();
  }
  redirects = () => {
    this.props.history.push("/");
  };

  toggle = () => {
    this.setState({ openMobileMenu: !this.state.openMobileMenu });
  };
  render() {
    return (
      <>
        <MobileMenu
          mobileactive={this.state.openMobileMenu}
          toggle={this.toggle}
        />
        <Navigation
          toggle={this.toggle}
          cartNum={this.props.cartNum}
          whiteColor={true}
        />
        <OverView
          paramlink={this.props.match.params.title}
          redirects={this.redirects}
        />
      </>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    cartNum: state.cartNum,
  };
};
export default connect(mapStateToProps, {
  fetchCartNumber,
})(AccountOverview);
