import React from "react";
import {
  OrderWrapper,
  OrderContainer,
  OrderTabs,
  TabsT,
  OrderContentOpen,
  OrderContentClosed,
  OrderWrap,
  OrderImage,
  OrderText,
  OrderT,
  OrderCode,
  OrderButton,
  EmptySection,
  EmptyCartContentWrap,
  EmptyCartContent,
  EmptyImage,
  EmptyText,
} from "./style";
import Cookies from "js-cookie";
import { connect } from "react-redux";
import { getClosedOrders, getOpenOrders } from "../../../store/actions";
import { MdLocalShipping } from "react-icons/md";
import { BASE_API_ } from "../../../utils/base-url.config";

class Orders extends React.Component {
  state = { active: "openOrders" };
  componentDidMount() {
    const getUserId = Cookies.get("sid");

    this.props.getClosedOrders(getUserId, this.props.redirects);
    this.props.getOpenOrders(getUserId, this.props.redirects);
  }
  changeActiveOrders = (name) => {
    this.setState({ active: name });
  };
  render() {
    
    return (
      <OrderWrapper>
        <OrderContainer>
          <OrderTabs>
            <TabsT
              name="openOrders"
              active={this.state.active}
              onClick={() => this.changeActiveOrders("openOrders")}
            >
              Open Orders
            </TabsT>
            <TabsT
              name="closedOrders"
              active={this.state.active}
              onClick={() => this.changeActiveOrders("closedOrders")}
            >
              Closed Orders
            </TabsT>
          </OrderTabs>

          <OrderContentOpen>
            {this.props.openOrders.length !== 0 ? (
              <>
                {this.props.openOrders.map((data, index) => {
                  return (
                    <OrderWrap>
                      <OrderImage>
                        <img
                          src={`${BASE_API_}${data.products[0].imageId.path}`}
                          alt=""
                        />
                      </OrderImage>
                      <OrderText>
                        <OrderT>{data.products[0].product_id.name}</OrderT>
                        <OrderCode>Order no. #{data.orderCode}</OrderCode>
                        <OrderButton>{data.delivery}</OrderButton>
                      </OrderText>
                    </OrderWrap>
                  );
                })}
              </>
            ) : (
              <EmptySection>
                <EmptyCartContentWrap>
                  <EmptyCartContent>
                    <EmptyImage>
                      <MdLocalShipping />
                    </EmptyImage>
                    <EmptyText>No Orders.</EmptyText>
                  </EmptyCartContent>
                </EmptyCartContentWrap>
              </EmptySection>
            )}
          </OrderContentOpen>

          <OrderContentClosed>
            {this.props.closedOrders.length !== 0 ? (
              <>
                {this.props.closedOrders.map((data, index) => {
                  return (
                    <OrderWrap>
                      <OrderImage>
                        <img
                          src={`${BASE_API_}${data.products[0].imageId.path}`}
                          alt=""
                        />
                      </OrderImage>
                      <OrderText>
                        <OrderT>{data.products[0].product_id.name}</OrderT>
                        <OrderCode>Order no. #{data.orderCode}</OrderCode>
                        <OrderButton>{data.delivery}</OrderButton>
                      </OrderText>
                    </OrderWrap>
                  );
                })}
              </>
            ) : (
              <EmptySection>
                <EmptyCartContentWrap>
                  <EmptyCartContent>
                    <EmptyImage>
                      <MdLocalShipping />
                    </EmptyImage>
                    <EmptyText>No Orders.</EmptyText>
                  </EmptyCartContent>
                </EmptyCartContentWrap>
              </EmptySection>
            )}
          </OrderContentClosed>
        </OrderContainer>
      </OrderWrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    openOrders: state.openOrders,
    closedOrders: state.closedOrders,
  };
};
export default connect(mapStateToProps, {
  getClosedOrders,
  getOpenOrders,
})(Orders);
