import { combineReducers } from "redux";
const featuredReducer = (state = [], action) => {
  switch (action.type) {
    case "FETCH_FEATURED":
      return action.payload;
    default:
      return state;
  }
};

const BlogReducer = (state = [], action) => {
  switch (action.type) {
    case "FETCH_BLOGS":
      return action.payload;
    default:
      return state;
  }
};

const TestimonialReducer = (state = [], action) => {
  switch (action.type) {
    case "FETCH_TESTIMONIAL":
      return action.payload;
    default:
      return state;
  }
};

const SocialsReducer = (state = {}, action) => {
  switch (action.type) {
    case "FETCH_SOCIALS":
      return action.payload;
    default:
      return state;
  }
};

const productReducer = (
  state = { products: [], totalProducts: "", current: "" },
  action
) => {
  switch (action.type) {
    case "FETCH_PRODUCTS":
      return action.payload;
    default:
      return state;
  }
};

const categoryReducer = (state = [], action) => {
  switch (action.type) {
    case "FETCH_CATEGORIES":
      return action.payload;
    default:
      return state;
  }
};

const productByCategoryReducer = (
  state = { products: [], totalProducts: "", current: "" },
  action
) => {
  switch (action.type) {
    case "FETCHBY_CATEGORY":
      return action.payload;
    default:
      return state;
  }
};

const changeCategoryReducer = (state = {}, action) => {
  switch (action.type) {
    case "CHANGE_CATEGORY":
      return action.payload;
    default:
      return state;
  }
};

const singleProduct = (state = {}, action) => {
  switch (action.type) {
    case "FETCH_SINGLE":
      return action.payload;
    default:
      return state;
  }
};

const cartProducts = (state = [], action) => {
  switch (action.type) {
    case "FETCH_CART":
      return action.payload;
    default:
      return state;
  }
};

const subTotalReducer = (state = [], action) => {
  switch (action.type) {
    case "FETCH_SUBTOTAL":
      return action.payload;
    default:
      return state;
  }
};

const fetchCartNumber = (state = [], action) => {
  switch (action.type) {
    case "FETCH_CART_NUMBER":
      return action.payload;
    default:
      return state;
  }
};

const fetchInfo = (state = {}, action) => {
  switch (action.type) {
    case "FETCH_INFO":
      return action.payload;
    default:
      return state;
  }
};

const userLogin = (state = "", action) => {
  switch (action.type) {
    case "LOG_USER":
      return action.payload;
    default:
      return state;
  }
};

const userDetails = (state = "", action) => {
  switch (action.type) {
    case "GETUSERDETAILS":
      return action.payload;
    default:
      return state;
  }
};

const productByRelatedReducer = (state = [], action) => {
  switch (action.type) {
    case "FETCHBY_RELATED":
      return action.payload;
    default:
      return state;
  }
};

const openOrderReducer = (state = [], action) => {
  switch (action.type) {
    case "FETCHOPEN_ORDERS":
      return action.payload;
    default:
      return state;
  }
};

const closedOrderReducer = (state = [], action) => {
  switch (action.type) {
    case "FETCHCLOSED_ORDERS":
      return action.payload;
    default:
      return state;
  }
};

const searchallProducts = (state = [], action) => {
  switch (action.type) {
    case "FETCH_SEARCH":
      return action.payload;
    default:
      return state;
  }
};

export default combineReducers({
  openOrders: openOrderReducer,
  closedOrders: closedOrderReducer,
  featured: featuredReducer,
  products: productReducer,
  category: categoryReducer,
  productCategory: productByCategoryReducer,
  selectedCategory: changeCategoryReducer,
  singleProduct: singleProduct,
  cart: cartProducts,
  subTotal: subTotalReducer,
  cartNum: fetchCartNumber,
  info: fetchInfo,
  logs: userLogin,
  userdetails: userDetails,
  related: productByRelatedReducer,
  searchallproducts: searchallProducts,
  blogs: BlogReducer,
  testimonial: TestimonialReducer,
  socials: SocialsReducer,
});
