import React from "react";
import {
  Wrapper,
  Container,
  ImageContainer,
  DetailContainer,
  SmallImageWrap,
  LargeImageWrap,
  DetailHead,
  DetailP,
  DetailText,
  DetailButton,
  Minus,
  Plus,
  RelateContainer,
  RelateHead,
  RelateContentWrap,
  RelateContent,
  RelateImage,
  RelateText,
  RelateP,
  LoadingWrapper,
  DetailColor,
  DetailSize,
  DetailFormWrap,
} from "./style";
import Loader from "react-loader-spinner";
import { BsPlus } from "react-icons/bs";
import { BiMinus } from "react-icons/bi";
import NoImage from '../../assets/NoImage.jpg'
import { BASE_API_ } from "../../utils/base-url.config";
//const cartFromLocalStorage = JSON.parse(localStorage.getItem('cart') || '[]');
class ProductListSection extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      quantity: 1,
      bigImage: this.props.product.featuredImage,
      cart: [],
      loading: false,
      color: "",
      stock: null,
      sizes: [],
      size: "",
      showImage: true,
    };
  }

  componentDidMount() {
    const cartFromLocalStorage = JSON.parse(
      localStorage.getItem("cart") || "[]"
    );
    this.setState({ cart: cartFromLocalStorage });
    if (cartFromLocalStorage.length === 0) {
      localStorage.setItem("cart", JSON.stringify(this.state.cart));
    }
  }

  componentDidUpdate(prevProps, state) {
    if (this.props.quantity !== prevProps.quantity) {
      this.setState({ quantity: 1 });
    }
    if (this.props.product.featuredImage !== prevProps.product.featuredImage) {
      this.setState({
        bigImage: this.props.product.featuredImage,
        sizes: this.props.product.sizes,
      });
    }

    if (this.state.cart.length !== state.cart.length) {
      localStorage.setItem("cart", JSON.stringify(this.state.cart));
      this.props.fetchCart();
    }
    if (this.props.product.productTypes !== prevProps.product.productTypes) {
      let stock = this.props.product.productTypes[0].Stock || 0;
     
      this.setState({
        color: this.props.product.productTypes[0].Colortype,
        stock: parseInt(stock),
        sizes: this.props.product.sizes,
      });

      if (this.props.product.productTypes[0].Images) {
        this.setState({
          Images: this.props.product.productTypes[0].Images,
          bigImage: this.props.product.productTypes[0].Images[0].imageId,
          productImage: this.props.product.productTypes[0].Images[0].imageId,
        });
      }
    }
  }

  manualChange = (e) => {
    
  };

  //product color selector change
  onChangeColor = (e) => {
    this.setState({ color: e.target.value }, () =>
      this.collectColor(e.target.value)
    );
  };

  //product color selector change
  onChangeSize = (e) => {
   
    this.setState({ size: e.target.value });
  };

  collectColor = (color) => {
    if (color) {
      this.props.product.productTypes.filter((data) => {
        if (data.Colortype === color) {
          let stock = data.Stock || 0;
          let image = data.Images.length > 0 ? data.Images[0].imageId : "";
          let imageShow = data.Images.length > 0 ? true : false;
          this.setState({
            Images: data.Images,
            bigImage: image,
            stock: parseInt(stock),
            productImage: image,
            showImage : imageShow,
          });
          return null;
        } else {
          return null;
        }
      });
    }
  };

  minusChange = () => {
    if (this.state.quantity > 1) {
      const value = this.state.quantity - 1;
      this.setState({ quantity: value });
    }
  };

  plusChange = () => {
    const value = this.state.quantity + 1;
    this.setState({ quantity: value });
  };

  changeBigImage = (data) => {
    this.setState({ bigImage: data });
  };
  onSubmit = (e) => {
    e.preventDefault();
  };

  addToCart = (product, quantity, color, productImage, size) => {
    //console.log('we are doing local', this.cartFromLocalStorage.length)
    let initialCart = this.state.cart;
    const checkProduct = initialCart.some((data) => {
      if (
        data._id === product._id &&
        data.colortype === color &&
        data.productSize === size
      ) {
        data.productSize = size;
        data.quantity = quantity + data.quantity;
        data.subtotal = data.quantity * data.price;
        data.productImage = productImage;
        localStorage.setItem("cart", JSON.stringify(initialCart));
        this.props.fetchCart();
        this.props.toggle();
        this.setState({ cart: initialCart, quantity: 1, loading: false });
        return data;
      }
      return null;
    });
    if (checkProduct) {
    } else {
      let products = {
        ...product,
      };
      products.productSize = size;
      products.quantity = quantity;
      products.subtotal = product.quantity * product.price;
      products.colortype = color;
      products.productImage = productImage;
      this.setState({
        cart: [...this.state.cart, products],
        quantity: 1,
        loading: false,
      });
      this.props.toggle();
    }
    // this.setState({ cart: [...this.state.cart, product] });
  };

  render() {
    return (
      <Wrapper>
        <Container>
          <ImageContainer>
            <SmallImageWrap>
              {this.state.Images ? (
                <>
                  {this.state.Images.map((data, index) => {
                    return (
                      <img
                        key={index}
                        onClick={() => this.changeBigImage(data.imageId)}
                        src={`${BASE_API_}${data.imageId.path}`}
                        alt={this.state.color}
                      />
                    );
                  })}
                </>
              ) : (
                <LoadingWrapper>
                  <Loader
                    type="TailSpin"
                    color="black"
                    height={30}
                    width={30}
                  />
                </LoadingWrapper>
              )}
            </SmallImageWrap>
            <LargeImageWrap>
              {this.state.showImage ? (
                <>
                  {this.state.bigImage ? (
                    <img
                      src={`${BASE_API_}${this.state.bigImage.path}`}
                      alt={this.state.bigImage.name}
                    />
                  ) : (
                    <LoadingWrapper>
                      {" "}
                      <Loader
                        type="TailSpin"
                        color="black"
                        height={50}
                        width={50}
                      />
                    </LoadingWrapper>
                  )}
                </>
              ) : (
                <img src={NoImage} alt="NoImage" />
              )}
            </LargeImageWrap>
          </ImageContainer>

          <DetailContainer>
            <DetailHead>{this.props.product.name}</DetailHead>
            <DetailP>
              <p>{this.props.product.price} KES</p>
              <h1>{this.state.stock > 0 ? "In stock" : "out of stock"}</h1>
            </DetailP>
            <DetailText>{this.props.product.description} </DetailText>
            <DetailColor>
              <label htmlFor="Color">Color</label>
              <div>
                <select
                  value={this.state.color}
                  name="color"
                  onChange={this.onChangeColor}
                  id="Color"
                >
                  <option value="">select an option</option>
                  {this.props.product.productTypes && (
                    <>
                      {this.props.product.productTypes.map((data, index) => {
                        return (
                          <option key={index} value={data.Colortype}>
                            {data.Colortype}
                          </option>
                        );
                      })}
                    </>
                  )}
                </select>
              </div>
            </DetailColor>

            {this.state.sizes.length > 0 && (
              <DetailSize>
                {" "}
                <label>Size</label>
                <div>
                  <select
                    value={this.state.size}
                    name="size"
                    onChange={this.onChangeSize}
                    id="Size"
                  >
                    {" "}
                    <option value="">select an option</option>
                    <>
                      {this.props.product.sizes.map((data, index) => {
                        return (
                          <option key={index} value={data}>
                            {data}
                          </option>
                        );
                      })}
                    </>
                  </select>
                </div>
              </DetailSize>
            )}

            {this.state.stock > 0 && (
              <>
                <DetailFormWrap>
                  <form onSubmit={this.onSubmit}>
                    <Minus onClick={this.minusChange}>
                      <BiMinus />
                    </Minus>
                    <input
                      type="text"
                      value={this.state.quantity}
                      onChange={this.manualChange}
                    />
                    <Plus onClick={this.plusChange}>
                      <BsPlus />
                    </Plus>
                  </form>
                </DetailFormWrap>

                {this.state.loading ? (
                  <DetailButton>
                    <Loader
                      type="TailSpin"
                      color="white"
                      height={30}
                      width={30}
                    />
                  </DetailButton>
                ) : (
                  <DetailButton
                    onClick={() =>
                      this.addToCart(
                        this.props.product,
                        this.state.quantity,
                        this.state.color ||
                          this.props.product.productTypes[0].Colortype,
                        this.state.productImage,
                        this.state.size || this.props.product.sizes[0]
                      )
                    }
                  >
                    ADD TO CART
                  </DetailButton>
                )}
              </>
            )}
          </DetailContainer>
        </Container>

        <RelateContainer>
          <RelateHead>You may also like the related products</RelateHead>
          <RelateContentWrap>
            {this.props.related.map((data, index) => {
              return (
                <RelateContent key={data._id} to={`/${data.name}/${data._id}`}>
                  <RelateImage>
                    {data.featuredImage ? (
                      <img
                        src={`${BASE_API_}${data.featuredImage.path}`}
                        alt={data.name}
                      />
                    ) : (
                      <img src={data.image} alt="" />
                    )}
                  </RelateImage>
                  <RelateText>{data.name}</RelateText>
                  <RelateP>{data.price} KES</RelateP>
                </RelateContent>
              );
            })}
          </RelateContentWrap>
        </RelateContainer>
      </Wrapper>
    );
  }
}

export default ProductListSection;
