import { useState, useEffect } from "react";
import Cookies from "js-cookie";
import axios from "axios";

import { BASE_API_ } from "../../utils/base-url.config";

const LoginDetails = (formsubmit, validate) => {
  const [values, setValues] = useState({
    username: "",
    password: "",
  });

  // eslint-disable-next-line no-unused-vars
  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);

  const handleChange = (e) => {
    if (
      e.target.name === "password" &&
      Object.keys(values.password).length < 6
    ) {
      setErrors({
        password: "password should be 6 characters",
      });
    } else if (
      e.target.name === "password" &&
      Object.keys(values.password).length >= 6
    ) {
      setErrors({
        password: undefined,
      });
    }
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validate(values));
    setIsSubmit(true);
  };

  useEffect(() => {
    if (Object.keys(errors).length === 0 && isSubmitting) {
      const loginFunc = async () => {
        try {
          await axios
            .post(`${BASE_API_}/user/v1/login`, values)
            .then((sucess) => {
              let data = sucess.data;
              let shipping = data.shipping;
              let userData = {
                firstname: data.firstname,
                lastname: data.lastname,
                email: data.email,
              };
              let shippingData = {
                ...userData,
                ...shipping,
                city: "",
              };
              Cookies.set("isLoggedIn", true, { expires: 1 });
              Cookies.set("_tk", data.token, { expires: 1 });
              Cookies.set("sid", data.id, { expires: 1 });
              Cookies.set("user", userData, { expires: 1 });
              Cookies.set("shipping", shippingData, { expires: 1 });
              formsubmit();
              let profile = `${BASE_API_}${data.profile.path}`;
              Cookies.set("profile", profile, {
                expires: 1,
              });
              setIsSubmit(false);
            })
            .catch((error) => {
              setSubmitError([error]);
              setIsSubmit(false);
            });
        } catch (error) {
          setIsSubmit(false);
        }
      };

      loginFunc();
    } else {
      setIsSubmit(false);
    }
  }, [errors, values, isSubmitting, formsubmit]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    isSubmitting,
  };
};

export default LoginDetails;
