/* eslint-disable react-hooks/exhaustive-deps */

import React, { useEffect } from "react";
import {
  Wrapper,
  Container,
  FormContactLabel,
  FormWrapper,
  ShippingLabel,
  FormContent,
  FormContent1,
  FormButton,
  FormError,
  FormContent2,
  LoginLink,
} from "./style";
import Loader from "react-loader-spinner";
import validate from "../../../validation";
import FormDetailCheck from "../../FormValidation/Form1";
const ShippingForm = ({ submitForm }) => {
  const {
    values,
    errors,
    handleChange,
    handleSubmit,
    getInfo,
    isLoggedIn,
    isSubmitting,
  } = FormDetailCheck(submitForm, validate);

  useEffect(() => {
    getInfo();
  }, []);

  return (
    <Wrapper>
      <Container>
        <form onSubmit={handleSubmit}>
          <FormWrapper>
            <FormContactLabel>
              <h1>Contact Information</h1>
              {!isLoggedIn && (
                <h5>
                  Already Have An Account?{" "}
                  <LoginLink to="/login">Login</LoginLink>
                </h5>
              )}
            </FormContactLabel>
            <input
              type="email"
              name="email"
              placeholder="email"
              value={values.email}
              onChange={handleChange}
            />
            {errors.email && <FormError>{errors.email}</FormError>}
          </FormWrapper>

          <ShippingLabel>
            <h1>Shipping Address</h1>
          </ShippingLabel>
          <FormContent1>
            <FormContent2>
              <input
                type="text"
                name="firstname"
                placeholder="firstname"
                value={values.firstname}
                onChange={handleChange}
              />
              {errors.firstname && <FormError>{errors.firstname}</FormError>}
            </FormContent2>
            <FormContent2>
              <input
                type="text"
                name="lastname"
                placeholder="lastname"
                value={values.lastname}
                onChange={handleChange}
              />
              {errors.lastname && <FormError>{errors.lastname}</FormError>}
            </FormContent2>
          </FormContent1>

          <FormContent>
            <input
              type="text"
              name="address"
              placeholder="Apartment, hse number, etc. "
              value={values.address}
              onChange={handleChange}
            />
            {errors.address && <FormError>{errors.address}</FormError>}
          </FormContent>

          {/** 
              <FormContent>
            <select name="city" value={values.city} onChange={handleChange}>
              <option className="cityColor">select city</option>
              {shippingCity.map((data)=>{
                return (
                 <option value={data._id}>{data.location}</option>  
                )
              })}
             
            
            </select>
            {errors.city && <FormError>{errors.city}</FormError>}
          </FormContent>
          */}

          <FormContent>
            <input
              type="text"
              name="country"
              placeholder="Country"
              value={values.country}
              onChange={handleChange}
            />
            {errors.country && <FormError>{errors.country}</FormError>}
          </FormContent>
          <FormContent>
            <input
              type="tel"
              placeholder="Phone"
              name="phonenumber"
              value={values.phonenumber}
              onChange={handleChange}
            />
            {errors.phonenumber && <FormError>{errors.phonenumber}</FormError>}
          </FormContent>
          {isSubmitting ? (
            <FormButton>
              {" "}
              <Loader type="TailSpin" color="white" height={30} width={30} />
            </FormButton>
          ) : (
            <FormButton type="submit">Proceed</FormButton>
          )}
        </form>
      </Container>
    </Wrapper>
  );
};

export default ShippingForm;
