import React from "react";
import { Wrapper, MenuWrapper, CloseIcon, MenuItem } from "./style";
import { GrClose } from "react-icons/gr";
import { connect } from "react-redux";
import {
  fetchCategories,
  changeCategory,
  fetchByCategory,
  fetchCartNumber,
  checkUser,
} from "../../../store/actions";

class MobileMenu extends React.Component {
  state = {};
  componentDidMount() {
    this.props.fetchCategory();
  }

  collectData = (id, name) => {
    this.props.changeCategory(id, name);
    this.props.toggle();
  };
  render() {
    return (
      <Wrapper mobileactive={this.props.mobileactive}>
        <CloseIcon onClick={this.props.toggle}>
          <GrClose />
        </CloseIcon>
        <MenuWrapper>
          <MenuItem to="/">Home</MenuItem>
          {this.props.categories.map((data, index) => {
            return (
              <MenuItem
                to={`/product/${data.name}/${data._id}`}
                onClick={() => this.collectData(data._id, data.name)}
                key={data._id}
              >
                {data.name}
              </MenuItem>
            );
          })}
        </MenuWrapper>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    categories: state.category,
    catNum: state.cartNum,
    logs: state.logs,
  };
};

export default connect(mapStateToProps, {
  fetchCategory: fetchCategories,
  changeCategory: changeCategory,
  fetchByCategory: fetchByCategory,
  fetchCartNumber: fetchCartNumber,
  checkUser,
})(MobileMenu);;
