import { useState, useEffect } from "react";
import Cookies from "js-cookie";


const FormDetails = (Formsubmit, validate) => {
  const [values, setValues] = useState({
    firstname: "",
    lastname: "",
    email: "",
    address: "",
    city: "",
    country: "",
    zip: "",
    phonenumber: "",
  });

  // eslint-disable-next-line no-unused-vars
  const [submitError, setSubmitError] = useState([]);

  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  //changing the input Values
  const handleChange = (e) => {
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validate(values));
    setIsSubmit(true);
  };

  const getInfo = () => {
    const collectInfo = async () => {
      try {
        const getToken = Cookies.get("_tk");
        const getId = Cookies.get("sid");
        const getShipping = JSON.parse(Cookies.get("shipping") || "{}");
        const getUser = JSON.parse(Cookies.get("user") || "{}");
        if (getToken && getId) {
          setValues({ ...values, ...getUser, ...getShipping });

          setIsLoggedIn(true);
        } else {
          const getInfo = JSON.parse(Cookies.get("contacts") || "{}");
          if (getInfo) {
            setValues({ ...values, ...getInfo });
            setIsLoggedIn(false);
          } else {
            setValues({ ...values });
            setIsLoggedIn(false);
          }
        }
      } catch (error) {
        let errorArray = [];
        errorArray.push(error);

        setSubmitError(errorArray);
      }
    };
    collectInfo();
  };

  useEffect(() => {
    if (Object.keys(errors).length === 0 && isSubmitting) {
      if (isLoggedIn) {
        Cookies.set("shipping", values, { expires: 1 });
        Cookies.set("contacts", values, { expires: 1 });
        // localStorage.setItem("contacts", JSON.stringify(values));
        Formsubmit();
      } else {
        Cookies.set("contacts", values, { expires: 1 });
        Formsubmit();
      }
    } else {
      setIsSubmit(false);
    }
  }, [errors, isSubmitting, values, isLoggedIn, Formsubmit]);

  return {
    values,
    errors,
    handleChange,
    handleSubmit,
    getInfo,
    isLoggedIn,
    isSubmitting,
 
  };
};

export default FormDetails;
