import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import GlobalStyle from './globalStyles';
import Home from './views/index';
import Products from './views/products';
import ProductLists from './views/productlist';
import Cart from './views/cart';
import Checkout from './views/checkout';
import Checkpay from './views/checkpay';
import Reset from './components/ResetPassword';
import Register from './views/Register';
import Login from './views/Login';
import ProductCategory from './views/productCategory';
import AccountOverview from './views/Aoverview';
import ProcessPay from './views/processPay';
import Success from './views/successPay';
import ProcessFailed from './views/ProcessFailed';
import ReturnRefund from './views/ReturnPolicy';
import TermsOfUse from './views/TermsPolicy';
import SearchPage from './views/searchView';
import ScrollTop from "./components/ScrollToTop";
import PassReset from './views/passreset';
import PassToken from './views/passtoken';
import PickUp from './views/pickupLocation';
import Page404 from './views/404'
function App() {
  return (
    <Router>
      <ScrollTop />
      <GlobalStyle />
      <Switch>
        <Route path="/" component={Home} exact />

        <Route path="/resetToken/:id" component={PassToken} exact />
        <Route path="/passreset/:token" component={PassReset} exact />
        <Route path="/products" component={Products} exact />
        <Route path="/product/:name/:id" component={ProductCategory} exact />
        <Route path="/overview/:title" exact component={AccountOverview} />
        <Route path="/:name/:id" exact component={ProductLists} />
        <Route path="/cart" component={Cart} />
        <Route path="/checkout" component={Checkout} />
        <Route path="/pickup" component={PickUp} />
        <Route path="/checkpay" component={Checkpay} />
        <Route path="/reset" component={Reset} />
        <Route path="/register" component={Register} />
        <Route path="/login" component={Login} />
        <Route path="/processing" component={ProcessPay} exact />
        <Route path="/processerror" component={ProcessFailed} exact />
        <Route path="/success" component={Success} exact />
        <Route path="/returnpolicy" component={ReturnRefund} exact />
        <Route path="/termsofuse" component={TermsOfUse} exact />
        <Route path="/search" component={SearchPage} exact />
        <Route component={Page404} exact />
      </Switch>
    </Router>
  );
}

export default App;
