import React from "react";
import {  FooterSection } from "../components/common";
import MobileMenu from "../components/common/MobileNav";
import Navigation from "../components/common/Navigation";
import ProductCategory from "../components/ProductCategory";

import { connect } from "react-redux";
import {
  fetchByCategory,
  fetchCartNumber,
  fetchSocials,
} from "../store/actions";

class ProductsCategory extends React.Component {
  state = {
    openMobileMenu: false,
    id: this.props.match.params.id,
    categoryName: this.props.match.params.name,
    itemsperPage: 8,
    loading: false,
    totalPages: null,
    current:null
  };

  componentDidMount() {
    let page;
    if (this.props.location.search) {
      page = this.props.location.search.split("").slice(3).join("");
     // console.log("pages", page);
    }

     const getProducts = async () => {
       this.setState({ loading: true });
       await this.props.fetchCartNumber();
       await this.props.fetchByCategory(this.state.id, page);
       await this.props.fetchSocials();
       this.setState({
         loading: false,
         totalPages: this.props.productsCollected.totalProducts,
         current: this.props.productsCollected.current,
       });
     };
     getProducts();
    
    
  }
  toggle = () => {
    this.setState({ openMobileMenu: !this.state.openMobileMenu });
  };

  componentDidUpdate(prevProps) {
    if ( this.props.selectedCategory.id !== prevProps.selectedCategory.id) {
      // window.location.reload();
      this.setState({ categoryName: this.props.selectedCategory.name });
       this.props.fetchByCategory(this.props.selectedCategory.id);
    }
  if(this.props.match.url === prevProps.match.url && this.props.location.search !== prevProps.location.search){
    let page;
    if (this.props.location.search) {
      page = this.props.location.search.split("").slice(3).join("");
      //console.log("pages", page);
    }

    const getProducts = async () => {
      this.setState({ loading: true });
      await this.props.fetchByCategory(this.state.id, page);
      this.setState({
        loading: false,
        totalPages: this.props.productsCollected.totalProducts,
        current: this.props.productsCollected.current,
      });
    };
    getProducts();

  }
    
  }

  render() {
   
    return (
      <>
        <MobileMenu
          mobileactive={this.state.openMobileMenu}
          toggle={this.toggle}
        />
        <Navigation
          toggle={this.toggle}
          cartNum={this.props.cartNum}
          unfixed={true}
        />
        <ProductCategory
          paramslinks={this.props.history}
          productsCollected={this.props.productsCollected.products}
          categoryName={this.state.categoryName}
          identity={this.state.id}
          productsPerPage={this.state.itemsperPage}
          current={this.props.productsCollected.current}
          totalProducts={this.props.productsCollected.totalProducts}
        />
        <FooterSection socials={this.props.socials} />
      </>
    );
  }
}

const mapStateToProps = (state) => {

  return {
    productsCollected: state.productCategory,
    selectedCategory: state.selectedCategory,
    cartNum: state.cartNum,
    socials: state.socials,
  };
};

export default connect(mapStateToProps, {
  fetchByCategory,
  fetchCartNumber,
  fetchSocials,
})(ProductsCategory);
