/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-unused-vars */
import { useState, useEffect } from "react";
import axios from "axios";
import Cookies from "js-cookie";
import { BASE_API_ } from "../../utils/base-url.config";

const AcFormDetails = (Formsubmit, validate) => {
  const [values, setValues] = useState({
    firstname: "",
    lastname: "",
    email: "",
    address: "",
    phonenumber: "",
  });

  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);
  const [profileImg, setProfileImg] = useState("");
  const [file, setFile] = useState("");
  const [successMsg, setSuccessMsg] = useState("");
  //changing the input Values
  const handleChange = (e) => {
    
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validate(values));
    setIsSubmit(true);
  };

  const profileHandler = (e) => {
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setFile(e.target.files[0]);
        setProfileImg(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };

  const getInfo = () => {
    const getInfo = JSON.parse(Cookies.get("user"));
    const getprofile = Cookies.get("profile");
    if (getInfo && getInfo !== {}) {
      setValues(getInfo);
      setProfileImg(getprofile);
    }
  };

  useEffect(() => {
   
    if (Object.keys(errors).length === 0 && isSubmitting) {
      const getId = Cookies.get("sid");
      const getToken = Cookies.get("_tk");
      const formData = new FormData();
      formData.append("file", file);
      formData.append("firstname", values.firstname);
      formData.append("lastname", values.lastname);
      formData.append("phonenumber", values.phonenumber);

      axios
        .put(`${BASE_API_}/user/v1/update/${getId}`, formData, {
          headers: {
            Authorization: "Bearer " + getToken,
            "Content-Type": "multipart/form-data",
          },
        })
        .then((success) => {
          setIsSubmit(false);
          Cookies.set("user", values, { expires: 1 });
          Cookies.set(
            "profile",
            `${BASE_API_}${success.data.profile.path}`,
            { expires: 1 }
          );
          setProfileImg(`${BASE_API_}${success.data.profile.path}`);
          setSuccessMsg("successfully updated");
          Formsubmit();
        })
        .catch((error) => {
          setSubmitError([error]);
          setIsSubmit(false);
        });
    } else {
     // setSubmitError([]);
      setIsSubmit(false);
    }
  }, [errors, isSubmitting, values, Formsubmit]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    getInfo,
    profileHandler,
    profileImg,
    isSubmitting,
    successMsg
  };
};

export default AcFormDetails;
