import React from "react";
import {
  Wrapper,
  Container,
  HeadWrap,
  LogoWrapper,
  LogoT,
  FormWrapper,
  FormHead,
  FormContent,
  ResetButton,
  FormButtonWrap,
  LoginLink,
  HeadContent,
  Success,
  ErrorMsg,
} from "./style";
import Loader from "react-loader-spinner";
import LogoImage from "../../assets/logoR.svg";
import validateReset from '../FormValidation/validateReset';
import FormResetPass from '../../components/FormValidation/passReset';
const ResetPassword = () => {

  const {
    values,
    errors,
    handleChange,
    handleSubmit,
    submitError,
    successMsg,
    isSubmitting,
  } = FormResetPass(validateReset);


  return (
    <Wrapper>
      <Container>
        <HeadWrap>
          <HeadContent>
            <LogoWrapper>
              <LogoT>
                <img src={LogoImage} alt="" />
              </LogoT>
            </LogoWrapper>
          </HeadContent>
        </HeadWrap>
        <FormWrapper>
          {successMsg && <Success><b>{successMsg}</b></Success>}
          {submitError.length !== 0 && submitError[0].response && (
            <ErrorMsg>{submitError[0].response.data}</ErrorMsg>
          )}
          {submitError.length !== 0 && !submitError[0].response && (
            <ErrorMsg>{submitError[0].message}, check your connection</ErrorMsg>
          )}
          <FormHead>
            <h1>Reset Your Password</h1>
            <p>
              We‚Äôll email you instructions to reset your password. If you don‚Äôt
              have access to your email contact our support team.
            </p>
          </FormHead>

          <form onSubmit={handleSubmit}>
            <FormContent>
              <label htmlFor="uname">username (*please use your email)</label>
              <input
                type="email"
                value={values.email}
                name="email"
                onChange={handleChange}
              />
              {errors.email && <ErrorMsg>{errors.email}</ErrorMsg>}
            </FormContent>

            <FormButtonWrap>
              {isSubmitting ? (
                <ResetButton>
                  {" "}
                  <Loader
                    type="TailSpin"
                    color="white"
                    height={30}
                    width={30}
                  />
                </ResetButton>
              ) : (
                <ResetButton type="submit">Reset password</ResetButton>
              )}

              <LoginLink to="/login">Return to login</LoginLink>
            </FormButtonWrap>
          </form>
        </FormWrapper>
      </Container>
    </Wrapper>
  );
};

export default ResetPassword;
