import React from "react";
import {
  ShopWrapper,
  SearchForm,
  IconButton,
  ProductResults,
  CloseButton,
  ProductContainer,
  ProductTitle,
  ProductContent,
  ProductImage,
  ProductDetails,
  ProductContentWrap,
  ProductContentContainer,
  ProductButton,
} from "./styles";
import SearchSvg from "../../../assets/Search.svg";
import { IoMdClose } from "react-icons/io";

import { connect } from "react-redux";
import { searchProducts } from "../../../store/actions";
import Cookies from "js-cookie";
import { BASE_API_ } from "../../../utils/base-url.config";

class SerachBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      query: "",
      results: [],
      loading: false,
      activedrop: true,
    };

    this.downRef = React.createRef();
    this.handleOutsideClick = this.handleOutsideClick.bind(this);
  }
  componentDidMount() {
    this.props.searchProducts();
    document.addEventListener("click", this.handleOutsideClick);
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.handleOutsideClick);
  }
  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleOutsideClick = (event) => {
    if (this.downRef && !this.downRef.current.contains(event.target)) {
      
      this.setState({ activedrop: false });
    } else {
      this.setState({ activedrop: true });
    }
  };

  getSearchResults =  (query) => {
    if (this.props.searchAllProducts !== 0) {
      const allProducts = this.props.searchAllProducts;
      const resultData =  allProducts.filter((products) => {
      
        let productName = products.name.toLowerCase();
        return productName.includes(query.toLowerCase());
      });
      const totalProducts = resultData.length;
       localStorage.setItem("searchResults", JSON.stringify(resultData));
       Cookies.set("searchquery", query);
      let showProducts = resultData;
      showProducts.splice(2);
      this.setState(
        {
          results: showProducts,
          loading: false,
          allResults: totalProducts,
        },
       
      );
    } else {
    }
  };
  
  handleInputChange = (event) => {
    const query = event.target.value;
   
    this.setState(
      {
        query: query,
        loading: true,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  render() {
    const { query } = this.state;
    return (
      <ShopWrapper>
        <SearchForm ref={this.downRef}>
          <form onSubmit={this.onFormSubmit}>
            <button>
              <IconButton>
                <img src={SearchSvg} alt="" />
              </IconButton>
            </button>
            <input
              type="text"
              value={query}
              name="query"
              onChange={this.handleInputChange}
            />
            <CloseButton onClick={this.props.close}>
              <IoMdClose />
            </CloseButton>
          </form>
        </SearchForm>
        {this.state.activedrop ? (
          <ProductResults>
            <ProductContainer>
              <ProductTitle>Products</ProductTitle>
              {this.state.results ? (
                <ProductContentWrap>
                  {this.state.results.map((data, index) => {
                    return (
                      <ProductContentContainer
                        key={data._id}
                        to={`/${data.name}/${data._id}`}
                      >
                        <ProductContent>
                          <ProductImage>
                            {!data.featuredImage ? null : (
                              <img
                                src={`${BASE_API_}${data.featuredImage.path}`}
                                alt=""
                              />
                            )}
                          </ProductImage>

                          <ProductDetails>
                            <h1>{data.name}</h1>
                            <h5>{data.price} KES</h5>
                          </ProductDetails>
                        </ProductContent>
                      </ProductContentContainer>
                    );
                  })}
                </ProductContentWrap>
              ) : null}
              {this.state.results.length !== 0 ? (
                <ProductButton to="/search">
                  VIEW RESULTS ({this.state.allResults})
                </ProductButton>
              ) : null}
            </ProductContainer>
          </ProductResults>
        ) : null}
      </ShopWrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    searchAllProducts: state.searchallproducts,
  };
};
export default connect(mapStateToProps, {
  searchProducts,
})(SerachBar);
