import React from "react";
import {
  Wrapper,
  Container,
  PHead,
  PSelection,
  SortWrap,
  PriceWrap,
  ProdSelection,
  ProdWrap,
  ProdContent,
  ProdImage,
  ProdText,
  ProdP,
  EmptySection,
  EmptyCartContentWrap,
  EmptyCartContent,
  EmptyImage,
  EmptyText,
} from "./style";
import Pagination from "./pagination2";
import { connect } from "react-redux";
import { fetchProducts } from "../../store/actions";
import { HiOutlineShoppingBag } from "react-icons/hi";
import { BASE_API_ } from "../../utils/base-url.config";


class ProductSection extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      itemsperPage: 8,
      loading: false,
      currentPage: 1,
      totalPages: null,
      items: [],
      sortOrder: "",
      sortedData: [],
    };
  }

  componentDidMount() {
    let page;
    if (this.props.page) {
      page = this.props.page.split("").slice(3).join("");
      
    }
    const getProducts = async () => {
      this.setState({ loading: true });
      await this.props.fetchProducts(page);
      this.setState(
        {
          items: this.props.productsCollected.products,
          loading: false,
          totalPages: this.props.productsCollected.totalProducts,
          current: this.props.productsCollected.current,
        },
        () => this.handleDataSorting()
      );
    };
    getProducts();
  }

  componentDidUpdate(prevProps) {
    if (this.props.page !== prevProps.page) {
      let page;
      if (this.props.page) {
        page = this.props.page.split("").slice(3).join("");
        
      }
      const getProducts = async () => {
        this.setState({ loading: true });
        await this.props.fetchProducts(page);
        this.setState(
          {
            items: this.props.productsCollected.products,
            loading: false,
            totalPages: this.props.productsCollected.totalProducts,
            current: this.props.productsCollected.current,
          },
          () => this.handleDataSorting()
        );
      };
      getProducts();
    }
  }

  handleDataSorting = () => {
    if (this.props.productsCollected.products.length !== 0) {
      if (this.state.sortOrder === "A-Z" || !this.state.sortOrder) {
        const dataSort = this.props.productsCollected.products.sort(function (
          productA,
          productB
        ) {
          if (productA.name.toLowerCase() < productB.name.toLowerCase())
            return -1;
          if (productA.name.toLowerCase() > productB.name.toLowerCase())
            return 1;
          return 0;
        });

        this.setState({ sortedData: dataSort });
      } else {
        const dataSort = this.props.productsCollected.products.sort(function (
          productA,
          productB
        ) {
          if (productA.name.toLowerCase() < productB.name.toLowerCase())
            return 1;
          if (productA.name.toLowerCase() > productB.name.toLowerCase())
            return -1;
          return 0;
        });

        this.setState({ sortedData: dataSort });
      }
    }
  };

  handleChange = (e) => {
    

    this.setState({ sortOrder: e.target.value });
    if (e.target.value === "Z-A") {
      if (this.props.productsCollected.products.length !== 0) {
        const dataSort = this.props.productsCollected.products.sort(function (
          productA,
          productB
        ) {
          if (productA.name.toLowerCase() < productB.name.toLowerCase())
            return 1;
          if (productA.name.toLowerCase() > productB.name.toLowerCase())
            return -1;
          return 0;
        });

        this.setState({ sortedData: dataSort });
      }
    } else {
      if (this.props.productsCollected.products.length !== 0) {
        const dataSort = this.props.productsCollected.products.sort(function (
          productA,
          productB
        ) {
          if (productA.name.toLowerCase() < productB.name.toLowerCase())
            return -1;
          if (productA.name.toLowerCase() > productB.name.toLowerCase())
            return 1;
          return 0;
        });

        this.setState({ sortedData: dataSort });
      }
    }
  };
  render() {
    return (
      <Wrapper>
        <Container>
          <PHead>
            <h1>Products</h1>
          </PHead>
          <PSelection>
            <SortWrap>
              <form>
                <label htmlFor="sort">SORT BY</label>
                <select id="sort" name="sort" onChange={this.handleChange}>
                  <option value="A-Z">Alphabetically, A-Z</option>
                  <option value="Z-A">Alphabetically, Z-A</option>
                </select>
              </form>
              <PriceWrap>
                <h1>{this.props.productsCollected.totalProducts} products</h1>
              </PriceWrap>
            </SortWrap>
          </PSelection>
        </Container>
        {this.props.productsCollected.products.length === 0 ? (
          <EmptySection>
            <EmptyCartContentWrap>
              <EmptyCartContent>
                <EmptyImage>
                  <HiOutlineShoppingBag />
                </EmptyImage>
                <EmptyText>No Products.</EmptyText>
              </EmptyCartContent>
            </EmptyCartContentWrap>
          </EmptySection>
        ) : (
          <ProdSelection>
            <ProdWrap>
              {this.state.sortedData.map((data, index) => {
                return (
                  <ProdContent key={data._id} to={`/${data.name}/${data._id}`}>
                    <ProdImage>
                      {data.featuredImage ? (
                        <img
                          src={`${BASE_API_}${data.featuredImage.path}`}
                          alt=""
                        />
                      ) : (
                        <img src={data.image} alt="" />
                      )}
                    </ProdImage>
                    <ProdText>{data.name}</ProdText>
                    <ProdP>{data.price} KES</ProdP>
                  </ProdContent>
                );
              })}
            </ProdWrap>
            <Pagination
              totalProducts={this.props.productsCollected.totalProducts}
              productsPerPage={this.state.itemsperPage}
              current={this.state.current}
              paramslinks={this.props.paramslinks}
            />
          </ProdSelection>
        )}
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return { productsCollected: state.products };
};

export default connect(mapStateToProps, { fetchProducts })(ProductSection);
