export default function validateInfo(values){
    let errors = {};

    if (!values.operator){
        errors.operator = 'please choose operator'
    }

    if(!values.number){
        errors.number = 'Please enter your phone number'
    } else if(isNaN(values.number)){
        errors.number = 'Phone number incorrect.'
    } else if(Object.keys(values.number).length !== 9){
        if (Object.keys(values.number).length === 10) {
            let newNumber = values.number
              .toString()
              .split("")
              .slice(1)
              .join("");
           
            errors.number = `number must be 9 digits. example: ${newNumber}`;
        }else {
             errors.number = "Must be 9 digits without the first zero(0)";
        }

    }

    return errors
}