export default function validateInfo(
  pickupAgent,
  agentLocation,
  validateAgents,
  cityName,
  courierName
) {
  let errors = {};

  if (!cityName) {
    errors.cityName = "city required";
  }

  if (!courierName) {
    errors.courierName = "courier required";
  }

  if (validateAgents) {
    if (!pickupAgent) {
      errors.pickupAgent = "pickupAgent required";
    }

    if (!agentLocation) {
      errors.agentLocation = "agentLocation required";
    }
  }

  return errors;
}
