import React from "react";
import {
  PaginationContainer2,
  PaginationWrapper2,
  PButton2,
  PageNum2,
  Num2,
  NButton2,
} from "./style";
import { CgArrowLeft, CgArrowRight } from "react-icons/cg";
const Pagination = ({
  totalProducts,
  productsPerPage,
  current,
  paramslinks,
  categoryName,
  identity,
}) => {
  
  const pageNum = [];

  for (
    let iteration = 1;
    iteration <= Math.ceil(totalProducts / productsPerPage);
    iteration++
  ) {
    pageNum.push(iteration);
  }

  const handleNext = () => {
    //adds to the current and moves to next page
    if (parseInt(current) + 1 <= pageNum.length) {
      let nextPage = parseInt(current) + 1;
      paramslinks.push(`/product/${categoryName}/${identity}?p=${nextPage}`);
    }
  };

  const handlePrev = () => {
    //adds to the current and moves to next page

   
    if (parseInt(current) > 1) {
      let PrevPage = parseInt(current) - 1;
      paramslinks.push(`/product/${categoryName}/${identity}?p=${PrevPage}`);

      //shifts to the hidden pageNumbers when end is reached
    }
  };

  return (
    <PaginationContainer2>
      <PaginationWrapper2>
        <PButton2 onClick={handlePrev}>
          <CgArrowLeft />
        </PButton2>
        <PageNum2>
          <Num2>
            Page {current} of {pageNum.length}
          </Num2>
        </PageNum2>
        <NButton2 onClick={handleNext}>
          <CgArrowRight />
        </NButton2>
      </PaginationWrapper2>
    </PaginationContainer2>
  );
};

export default Pagination;
