import React from "react";
import {
  Wrapper,
  Container,
  CartHead,
  CloseIcon,
  ProductSection,
  ProductContainer,
  ProductImage,
  ProductContent,
  ProductTitle,
  ProductQuantity,
  ProductPriceWrap,
  ProductPrice,
  RemoveButton,
  ProductSubTotal,
  Total,
  ProductButtonWrap,
  ViewCart,
  Checkout,
  ProductWrap,
  ProductP,
} from "./style";

import { GrClose } from "react-icons/gr";
import NoImage from "../../../assets/NoImage.jpg";
import { BASE_API_ } from "../../../utils/base-url.config";

const MiniCart = ({ cart, removeItem, subTotal, miniCartDisplay, toggle }) => {
  return (
    <Wrapper minidisplay={miniCartDisplay}>
      <Container>
        <CartHead>
          <h1>Your Cart</h1>
          <CloseIcon onClick={toggle}>
            <GrClose />
          </CloseIcon>
        </CartHead>
        <hr />

        <ProductSection>
          {cart.map((data, index) => {
            let pName = data.name;
            if (Object.keys(pName).length > 20) {
              pName = data.name.slice(0, 20) + "...";
            }
            return (
              <ProductWrap key={index}>
                <ProductContainer>
                  <ProductImage>
                    {data.productImage ? (
                      <img
                        src={`${BASE_API_}${data.productImage.path}`}
                        alt={data.productImage.name}
                      />
                    ) : (
                      <img src={NoImage} alt="NoImage" />
                    )}
                  </ProductImage>

                  <ProductContent>
                    <ProductTitle>{pName}</ProductTitle>
                    {data.colortype && (
                      <ProductP>Color: {data.colortype}</ProductP>
                    )}

                    {data.productSize && (
                      <ProductP>
                        Size: {data.productSize ? data.productSize : null}
                      </ProductP>
                    )}

                    <ProductQuantity>Qty : {data.quantity}</ProductQuantity>
                    <ProductPriceWrap>
                      <ProductPrice>{data.price} KES</ProductPrice>
                      <RemoveButton onClick={() => removeItem(data._id)}>
                        Remove
                      </RemoveButton>
                    </ProductPriceWrap>
                  </ProductContent>
                </ProductContainer>
                <hr />
              </ProductWrap>
            );
          })}
        </ProductSection>

        <ProductSubTotal>
          <h1>Subtotal</h1>
          <Total>{subTotal} KES</Total>
        </ProductSubTotal>
        <hr />

        <ProductButtonWrap>
          <ViewCart to="/cart">VIEW CART</ViewCart>

          <Checkout to="/checkout">CHECKOUT</Checkout>
        </ProductButtonWrap>
      </Container>
    </Wrapper>
  );
};

export default MiniCart;
