import React, { useEffect} from "react";
import {
  Wrapper,
  Container,
  ProfileWrapper,
  FormContent,
  FormContentWrap,
  FormButton,
  Profile,
  ProfileButton,
  Success,
  ErrorMsg,
} from "./style";
import icon from "../../../assets/upload.png";
import user from "../../../assets/feather_user.png";
import AcFormDetails from "../../FormValidation/acdetail";
import { validateInfo } from "../../FormValidation/validateAcdetail";
import Cookies from 'js-cookie';
import Loader from "react-loader-spinner";
const AccountDetails = ({ redirects }) => {
  const formSubmit = () => {};
  const {
    values,
    // eslint-disable-next-line no-unused-vars
    errors,
    // eslint-disable-next-line no-unused-vars
    submitError,
    handleChange,
    handleSubmit,
    getInfo,
    profileHandler,
    profileImg,
    isSubmitting,
    successMsg,
  } = AcFormDetails(formSubmit, validateInfo);
  useEffect(() => {
    let getLogin = Cookies.get("isLoggedIn");
    if (getLogin === "true") {
         getInfo();
    } else {
      redirects();
    }
 

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Wrapper>
      <Container>
        <form onSubmit={handleSubmit}>
          {successMsg && <Success>{successMsg}</Success>}
          {submitError.length !== 0 && submitError[0].response && (
            <ErrorMsg>{submitError[0].response.data}</ErrorMsg>
          )}
          {submitError.length !== 0 && !submitError[0].response && (
            <ErrorMsg>{submitError[0].message}, check your connection</ErrorMsg>
          )}
          <FormContentWrap>
            <FormContent>
              <label>First name</label>
              <input
                type="text"
                name="firstname"
                onChange={handleChange}
                value={values.firstname}
              />
            </FormContent>
            <FormContent>
              <label>last name</label>
              <input
                type="text"
                name="lastname"
                onChange={handleChange}
                value={values.lastname}
              />
            </FormContent>
          </FormContentWrap>
          <FormContentWrap>
            <FormContent>
              <label>Email</label>
              <input
                type="email"
                name="email"
                onChange={handleChange}
                value={values.email}
              />
            </FormContent>
            <FormContent>
              <label>Phone number</label>
              <input
                type="text"
                value={values.phonenumber}
                onChange={handleChange}
                name="phonenumber"
              />
            </FormContent>
          </FormContentWrap>
          {isSubmitting ? (
            <FormButton>
              {" "}
              <Loader type="TailSpin" color="white" height={30} width={30} />
            </FormButton>
          ) : (
            <FormButton type="submit">Save Changes</FormButton>
          )}
        </form>
        <ProfileWrapper>
          <Profile>
            {profileImg ? (
              <img className="profile" src={profileImg} alt="" />
            ) : (
              <img className="default" src={user} alt="" />
            )}
          </Profile>
          <input
            type="file"
            name="profileUpload"
            id="upload"
            accept="image/*"
            onChange={profileHandler}
          />
          <label htmlFor="upload" className="profileUpload">
            <ProfileButton>
              <img src={icon} alt="" /> Upload
            </ProfileButton>
          </label>
        </ProfileWrapper>
      </Container>
    </Wrapper>
  );
};

export default AccountDetails;
