import React from "react";
import {
  AccountWrap,
  DetailsWrap,
  Details,
  DetailHead,
  Information,
  DetailSection,
  TextWrap,
  ButtonWrap,
  InformationHead,
  InformationSection,
  InfoTextWrap,
  InfoButtonWrap,
  Line,
  PassWrapper,
  PassText,
  PassButton
} from "./style";
import Cookies from "js-cookie";
import { connect } from "react-redux";
import { getUserDetails } from "../../../store/actions";

class Account extends React.Component {
  state = { users: {}, shipping: {} };
  componentDidMount() {
    
    this.props.getUserDetails(this.props.redirects);
    // console.log(this.props.getUser, Object.entries(this.state.shipping));
     if (this.props.getUser.user) {
         Cookies.set("user", this.props.getUser.user, { expires: 1 });
       this.setState({ users: this.props.getUser.user });
      
     } else {
       this.setState({ users: {} });
     }
   if(this.props.getUser.shipping === null || this.props.getUser.shipping === undefined){
     this.setState({shipping: {}})
   }else {
     this.setState({ shipping: this.props.getUser.shipping });
     Cookies.set("shipping", this.props.getUser.shipping, { expires: 1 });
     
   }
  
  }

  componentDidUpdate(prevProps, state) {
    if (this.props.getUser.user !== prevProps.getUser.user) {
     
     if (this.props.getUser.user) {
         Cookies.set("user", this.props.getUser.user, { expires: 1 });
       this.setState({ users: this.props.getUser.user });
     } else {
       this.setState({ users: {} });
     }
     if (
       this.props.getUser.shipping === null ||
       this.props.getUser.shipping === undefined
     ) {
       this.setState({ shipping: {} });
     } else {
       Cookies.set("shipping", this.props.getUser.shipping, { expires: 1 });
       this.setState({ shipping: this.props.getUser.shipping });

     }
    }
  }
  
  render() {
    return (
      <AccountWrap>
        <DetailsWrap>
          <Details>
            <DetailHead>
              <h1>ACCOUNT DETAILS</h1>
            </DetailHead>
            <DetailSection>
              {Object.entries(this.state.users).length !== 0 ? (
                <TextWrap>
                  <h1>
                    {this.state.users.firstname} {this.state.users.lastname}
                  </h1>
                  <h1>{this.state.users.email}</h1>
                </TextWrap>
              ) : (
                <TextWrap>
                  <h1>firstname lastname</h1>
                  <h1>email</h1>
                </TextWrap>
              )}

              <ButtonWrap to="/overview/accdetails">Change</ButtonWrap>
            </DetailSection>
          </Details>
          <Information>
            <InformationHead>
              <h1>Address Information</h1>
            </InformationHead>
            <InformationSection>
              {Object.entries(this.state.shipping).length !== 0 ? (
                <InfoTextWrap>
                  <h1>{this.state.shipping.postbox}</h1>
                  <h1>{this.state.shipping.address}</h1>
                  <h1>
                    {this.state.shipping.city}, {this.state.shipping.country}
                  </h1>
                </InfoTextWrap>
              ) : (
                <InfoTextWrap>
                  <h1>add Post office number</h1>
                  <h1>add physical address</h1>
                  <h1>add city and country</h1>
                </InfoTextWrap>
              )}

              <InfoButtonWrap to="/overview/addressinfo">Change</InfoButtonWrap>
            </InformationSection>
          </Information>
        </DetailsWrap>

        <Line />

        <PassWrapper>
          <PassText>
            <h1>Password</h1>
            <p>You can reset or change your password by clicking here</p>
          </PassText>
          <PassButton to="/overview/changepaskey">Change</PassButton>
        </PassWrapper>
      </AccountWrap>
    );
  }
}
const mapStateToProps = (state) => {
  return { getUser: state.userdetails };
};
export default connect(mapStateToProps, { getUserDetails })(Account);
