import React from "react";
import Search from "../components/SearchPage";
import MobileMenu from "../components/common/MobileNav";
import Navigation from "../components/common/Navigation";
import { FooterSection } from "../components/common";

import { connect } from "react-redux";
import { fetchCartNumber, fetchSocials } from "../store/actions";
class SearchView extends React.Component {
  state = { openMobileMenu: false };
  componentDidMount() {

    this.props.fetchCartNumber();
    this.props.fetchSocials();
  }
 

  toggle = () => {
    this.setState({ openMobileMenu: !this.state.openMobileMenu });
  };
  render() {
    return (
      <>
        <MobileMenu
          mobileactive={this.state.openMobileMenu}
          toggle={this.toggle}
        />
        <Navigation toggle={this.toggle} cartNum={this.props.cartNum} />
        <Search />
        <FooterSection socials={this.props.socials} />
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    cartNum: state.cartNum,
    socials: state.socials,
  };
};
export default connect(mapStateToProps, {
  fetchCartNumber,
  fetchSocials,
})(SearchView);
