import React from 'react'
import {  FooterSection } from "../components/common";
import MobileMenu from "../components/common/MobileNav";
import Navigation from "../components/common/Navigation";
import CartSection from "../components/Cart";
import { connect } from "react-redux";
import {
  fetchCartProducts,
  fetchSubTotal,
  fetchCartNumber,
  fetchSocials,
} from "../store/actions";

class Cart extends React.Component {
  state = {
    openMobileMenu: false,
  };

  componentDidMount() {
    this.props.fetchCartProducts();
    this.props.fetchSubTotal();
    this.props.fetchCartNumber();
    this.props.fetchSocials();
  }
  toggle = () => {
    this.setState({ openMobileMenu: !this.state.openMobileMenu });
  };

  removeItem = (id) => {
   
    let initialCart = this.props.cart;

    initialCart.some((data, index) => {
      if (data._id === id) {
        initialCart.splice(index, 1);
        localStorage.setItem("cart", JSON.stringify(initialCart));
        
        window.location.reload();
        return data;
      }
      return null;
    });
  };
  render() {
    return (
      <>
        <MobileMenu
          mobileactive={this.state.openMobileMenu}
          toggle={this.toggle}
        />
        <Navigation toggle={this.toggle} cartNum={this.props.cartNum} />
        <CartSection
          cartNum={this.props.cartNum}
          subTotal={this.props.subTotal}
          cart={this.props.cart}
          removeItem={this.removeItem}
        />
        <FooterSection socials={this.props.socials} />
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    products: state.singleProduct,
    cart: state.cart,
    subTotal: state.subTotal,
    cartNum: state.cartNum,
    socials: state.socials,
  };
};
export default connect(mapStateToProps, {
  fetchCartNumber,
  fetchSubTotal,
  fetchCartProducts,
  fetchSocials,
})(Cart);
